import {PropTypes} from 'prop-types';
import React, {useEffect, useRef, useState} from 'react';
import {Button} from './../../components/UI';
import {TextInput} from 'react-native-paper';
import {
  Animated,
  Image,
  ImageBackground,
  Text,
  View,
  Platform,
  ScrollView,
  TouchableOpacity,
} from 'react-native';
import {questionsKometa} from '../../config/utils/shareClass';

// Styles
import {homeMainStyles as styles} from '../auth/auth.styles';
import StylesVariables from '../../../src/config/Styles/app.style';

// Redux
import {RENDER_LAYOUT, RENDER_MESSAGE, AUTH_USER} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import ShopClass from './../../services/Shop.class';

const beneficiosArr = [
  {
    title: 'Presencia digital a tu emprendimiento',
    image: require('../../../assets/shopping.png'),
  },
  {
    title: 'Encuentra proveedores de confianza en el almacén',
    image: require('../../../assets/transist.png'),
  },
  {
    title: 'Conoce nuevas formas de ganar más dinero',
    image: require('../../../assets/cash.png'),
  },
  {
    title: 'Conecta con una comunidad de emprendedores',
    image: require('../../../assets/community.png'),
  },
];

const prospects = [
  {
    title: 'Lorena // Cosméticos Deysi',
    body: 'Con Kometa comparto mi tienda de todos mis cosméticos, y ya tengo varios pedidos directo a mi WhatsApp, es súper fácil',
    image: require('../../../assets/kometa_ima1.jpg'),
  },
  {
    title: 'Carlos // La Casa de la mochila',
    body: 'Estoy iniciando mi negocio y Kometa me ha permitido promocionar mis productos de manera gratuita y compartirlos',
    image: require('../../../assets/kometa_ima2.jpg'),
  },
  {
    title: 'Andrés // Flores Andy',
    body: 'Mi experiencia ha sido muy buena, encontré un proveedor a menor costo y ahora invierto en más producto y llego a más clientes',
    image: require('../../../assets/kometa_ima3.jpg'),
  },
];
const emailValidate =
  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

const HomeScreen = ({navigation}) => {
  const dispach = useDispatch();
  const [email, setEmail] = useState('');
  useEffect(() => {
    setTimeout(() => {
      fadeIn();
    }, 500);
    verifyIfOnSessionUser();
  }, []);
  const [sessionNumber, setSessionNumber] = useState();
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const moveBottom = useRef(new Animated.Value(50)).current;

  const fadeIn = () => {
    Animated.timing(moveBottom, {
      toValue: 0,
      duration: 500,
    }).start();
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 500,
    }).start();
  };

  const socialMediaRef = (social) => {
    if (social == 'wsp') {
      questionsKometa();
      return;
    };
    window.open(
        'https://www.facebook.com/KometaMexico',
        '_blank',
    );
  };

  const verifyIfOnSessionUser = async () => {
    let token;
    if (Platform.OS === 'web') {
      token = localStorage.getItem('kometa-token');
    } else {
      token = await AsyncStorage.getItem('kometa-token');
    };
    setSessionNumber(token);
  };
  const sendEmail = async () => {
    if (!email.match(emailValidate)) {
      dispach({
        message: {
          show: true,
          message: 'Porfavor, introduzca una dirección válida',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
      return;
    };
    const eClass = new ShopClass();
    try {
      dispach({
        layout: {
          layout: true,
        },
        type: RENDER_LAYOUT,
      });
      await eClass.getEmailAddress(email);
      dispach({
        message: {
          show: true,
          message: 'Nos contactaremos contigo a la brevedad!',
          status: 'S',
        },
        type: RENDER_MESSAGE,
      });
    } catch (e) {
      dispach({
        message: {
          show: true,
          message: '',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const moveToStore = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    const shop = new ShopClass();
    try {
      const userInfo = await shop.getShopByNumber(sessionNumber);
      dispach({
        userInfo: {
          number: userInfo.phoneNumber,
          userInfo,
        },
        type: AUTH_USER,
      });
    } catch (e) {
      dispach({
        message: {
          show: true,
          message: 'Sesion caducada, intentelo de nuevo.',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  return (
    <Animated.View
      style={[
        styles.container,
        {
          opacity: fadeAnim,
          bottom: moveBottom,
        },
      ]}
    >
      <ScrollView
        style={styles.scrollContainer}
        keyboardDismissMode={'interactive'}
        keyboardShouldPersistTaps={'handled'}
        contentContainerStyle={styles.contentContainer}
      >
        <View
          style={styles.responsiveContainer}
        >
          <View
            style={styles.header}
          >
            <View
              style={styles.headerLeft}
            >
              <Image
                source={require('../../../assets/kometa-02.png')}
                style={styles.image}
                resizeMode={'contain'}
              />
            </View>
            <View
              style={styles.headerRigth}
            >
              {
                sessionNumber &&
                  <Button
                    type={'white'}
                    title={'Ir a mi tienda'}
                    onPress={() => moveToStore()}
                  />
              }
            </View>
          </View>
          <video
            controls={false}
            autoPlay={true}
            muted={true}
            width='100%'
            height={'auto'}
            loop={true}
            poster={require('../../../assets/fondovideokometa.png')}
          >
            <source
              src={require('../../../assets/videoLanding.mp4')+'#t=5'}
              type="video/mp4"
            />
          </video>
          <View
            style={styles.bodyContainer}
          >
            <Text
              style={styles.cardTitle}
            >
              ¡Crea, comparte tus productos
              por whatsApp
              <Text
                style={[
                  styles.cardTitle,
                  {color: '#00D6AD'},
                ]}
              >
                {' y vende más'}
              </Text>
              !
            </Text>
            <View
              style={styles.buttonContainer}
            >
              <Button
                id={'home_screen_init_button'}
                category={'button'}
                action={'click'}
                uppercase={false}
                onPress={() => navigation.navigate('WhatsappRegister')}
                title='Crea tu tienda ahora'
                fullWidth={true}
                mode={'buttonsGColors'}
              />
              <Text
                style={styles.texButtonAnotation}
              >
                Sin costo en menos de 3 min.
              </Text>
            </View>
            <View
              style={styles.row}
            >
              <View
                style={{flex: .15}}
              >
                <Image
                  source={require('../../../assets/icon.png')}
                  style={{height: 60, width: 60}}
                  resizeMode={'contain'}
                />
              </View>
              <View
                style={{flex: .85}}
              >
                <Text
                  style={styles.beneficiosTitle}
                >
                  Beneficios de ser parte de
                  Kometa.shop
                </Text>
              </View>
            </View>
            <View
              style={styles.row}
            >
              {
                beneficiosArr.map((item, index) => {
                  return (
                    <View
                      style={styles.boxContainer}
                      key={index}
                    >
                      <View
                        style={styles.boxExterior}
                      />
                      <View
                        key={index}
                        style={styles.box}
                      >
                        <Image
                          source={item.image}
                          style={{width: 64, height: 64}}
                        />
                        <Text
                          style={styles.textBox}
                        >
                          {item.title}
                        </Text>
                      </View>
                    </View>
                  );
                })
              }
            </View>
            <View
              style={styles.buttonContainer}
            >
              <Button
                id={'home_screen_init_button'}
                category={'button'}
                action={'click'}
                uppercase={false}
                onPress={() => navigation.navigate('WhatsappRegister')}
                title='Crea tu tienda ahora'
                fullWidth={true}
                mode={'buttonsGColors'}
              />
            </View>
            <View
              style={styles.row}
            >
              <View
                style={{flex: .15}}
              >
                <Image
                  source={require('../../../assets/icon.png')}
                  style={{height: 60, width: 60}}
                  resizeMode={'contain'}
                />
              </View>
              <View
                style={{flex: .85}}
              >
                <Text
                  style={styles.beneficiosTitle}
                >
                  Testimoniales
                </Text>
              </View>
            </View>
            <View
              style={styles.row}
            >
              {
                prospects.map((item, index) => {
                  return (
                    <View
                      key={index}
                      style={styles.boxProspects}
                    >
                      <View
                        style={styles.quoteBaxkground}
                      >
                        <Image
                          source={require('../../../assets/comillas.png')}
                          style={{width: 80, height: 80}}
                          resizeMode={'contain'}
                        />
                      </View>
                      <View
                        style={styles.prospectsHeader}
                      >
                        <View
                          style={styles.prospectPhoto}
                        >
                          <ImageBackground
                            source={item.image}
                            style={{width: '100%', height: '100%'}}
                          />
                        </View>
                      </View>
                      <Text
                        style={styles.prospectsTitle}
                      >
                        {item.title}
                      </Text>
                      <Text
                        style={styles.prospectsDescription}
                      >
                        {item.body}
                      </Text>
                    </View>
                  );
                })
              }
            </View>
          </View>
          <View
            style={styles.footer}
          >
            <View
              style={styles.footerRow}
            >
              <Text
                style={styles.needHelpText}
              >
                ¿Necesitas más información?
              </Text>
            </View>
            <View
              style={styles.footerRow}
            >
              <View
                style={{flex: .7}}
              >
                <TextInput
                  value={email}
                  mode='outlined'
                  placeholder='Ingresa tu Email'
                  onChangeText={(value) => {
                    setEmail(value);
                  }}
                  maxLength={30}
                  activeOutlineColor={StylesVariables.Kometa}
                  style={styles.input}
                />
              </View>
              <View
                style={{flex: .3}}
              >
                <Button
                  title='Enviar'
                  onPress={() => sendEmail()}
                  fullWidth={true}
                  out={true}
                />
              </View>
            </View>
            <View
              style={styles.footerRow}
            >
              <View
                style={{flex: 1}}
              >
                <Text
                  style={styles.socialMediaText}
                >
                  Nuestras redes sociales....
                </Text>
              </View>
              <View
                style={{
                  flex: 1,
                  alignItems: 'center',
                  justifyContent: 'flex-end',
                  flexDirection: 'row',
                }}
              >
                <TouchableOpacity
                  style={{
                    marginHorizontal: 5,
                  }}
                  onPress={() => socialMediaRef('wsp')}
                >
                  <View
                    style={styles.socialButtons}
                  >
                    <Image
                      source={require('../../../assets/wsp.png')}
                      style={{width: 25, height: 25}}
                    />
                  </View>
                </TouchableOpacity>
                <TouchableOpacity
                  style={{
                    marginHorizontal: 5,
                  }}
                  onPress={() => socialMediaRef('fb')}
                >
                  <View
                    style={styles.socialButtons}
                  >
                    <Image
                      source={require('../../../assets/fb.png')}
                      style={{width: 25, height: 25}}
                    />
                  </View>
                </TouchableOpacity>
              </View>
            </View>
          </View>
        </View>
      </ScrollView>
    </Animated.View>
  );
};

HomeScreen.propTypes = {
  navigation: PropTypes.any,
};
export default HomeScreen;
