import PropTypes from 'prop-types';
import React from 'react';
import {
  ImageBackground,
  StyleSheet,
  TouchableOpacity,
  View,
} from 'react-native';
import {Card, Text} from 'react-native-paper';

// icons
import {AntDesign} from '@expo/vector-icons';

// Styles
import StylesVariables from '../config/Styles/app.style';

const Product = ({
  product,
  action,
  addCartAction,
  items = 0,
  client = false
}) => {
  return (
    <Card
      style={{marginBottom: 5, width: '100%'}}
      onPress={action}
    >
      <View
        style={styles.container}
      >
        <View
          style={{flex: .3}}
        >
          <View
            style={styles.imageContainer}
          >
            <ImageBackground
              source={
                !product.images ||product.images.length === 0 ?
                require('../../assets/icons/productGeneric.png') :
                {uri: product.images[0]}
              }
              style={{
                width: '100%',
                height: '100%',
              }}
            />
          </View>
        </View>
        <View
          style={styles.TextContainer}
        >
          <Text
            style={styles.titleProduct}
          >
            {product.productName}
          </Text>
          <View>
            <Text
              style={styles.descriptionText}
              numberOfLines={1}
            >
              {product.productDescription}
            </Text>
          </View>
          <View
            style={styles.priceContainer}
          >
            <View
              style={{flex: .65}}
            >
              <Text
                style={styles.priceText}
              >
                ${product.price}
              </Text>
            </View>
            {
              client &&
                <View
                  style={styles.gestorItemsContainer}
                >
                  <TouchableOpacity
                    style={{
                      marginHorizontal: 8,
                    }}
                    onPress={() => addCartAction(product, 'dimis')}
                  >
                    <View
                      style={styles.buttonsRadius}
                    >
                      <AntDesign
                        name="minus"
                        size={20 * StylesVariables.responsiveHeightMulti}
                        color={StylesVariables.Kometa}
                      />
                    </View>
                  </TouchableOpacity>
                  <Text
                    style={styles.radiusText}
                  >
                    {items}
                  </Text>
                  <TouchableOpacity
                    style={{
                      marginHorizontal: 8,
                    }}
                    onPress={() => addCartAction(product, 'add')}
                  >
                    <View
                      style={styles.buttonsRadius}
                    >
                      <AntDesign
                        name="plus"
                        size={20 * StylesVariables.responsiveHeightMulti}
                        color={StylesVariables.Kometa}
                      />
                    </View>
                  </TouchableOpacity>
                </View>
            }
          </View>
        </View>
      </View>
    </Card>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    borderRadius: 5 * StylesVariables.responsiveHeightMulti,
    maxHeight: 120 * StylesVariables.responsiveHeightMulti,
    flexDirection: 'row',
    padding:
      StylesVariables.spacing * 1.5 * StylesVariables.responsiveHeightMulti,
    flexWrap: 'wrap',
    overflow: 'hidden',
  },
  imageContainer: {
    width: 72 * StylesVariables.responsiveHeightMulti,
    height: 72 * StylesVariables.responsiveHeightMulti,
    borderRadius: 8 * StylesVariables.responsiveHeightMulti,
    overflow: 'hidden',
  },
  TextContainer: {
    padding: 5,
    flexWrap: 'wrap',
    flex: .7,
  },
  titleProduct: {
    fontFamily: 'SemiBold',
    fontSize: 16 * StylesVariables.responsiveHeightMulti,
    color: StylesVariables.Asteroide,
  },
  descriptionText: {
    ...StylesVariables.appTextMinor,
    color: StylesVariables.Asteroide80p,
    fontSize: 12 * StylesVariables.responsiveHeightMulti,
    flex: 1,
    width: '55%',
  },
  priceText: {
    fontFamily: 'NunitoBold',
    color: StylesVariables.KometaDark,
    fontSize: 20 * StylesVariables.responsiveHeightMulti,
  },
  gestorItemsContainer: {
    flex: .35,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  priceContainer: {
    width: '100%',
    flexDirection: 'row',
  },
  radiusText: {
    ...StylesVariables.appTextMedium,
    marginHorizontal: 5,
    fontSize: 12 * StylesVariables.responsiveHeightMulti,
    color: StylesVariables.Bajio,
  },
  buttonsRadius: {
    width: 23 * StylesVariables.responsiveHeightMulti,
    height: 23 * StylesVariables.responsiveHeightMulti,
    borderRadius: 23 * StylesVariables.responsiveHeightMulti,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: StylesVariables.Blanco,
    borderColor: StylesVariables.Kometa,
    borderWidth: 1.5,
  },
});
Product.propTypes = {
  navigation: PropTypes.any,
  product: PropTypes.object,
  action: PropTypes.func,
  addCartAction: PropTypes.func,
  items: PropTypes.any,
  client: PropTypes.bool,
};

export default Product;
