import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, View} from 'react-native';
import {TextInput as InputText, Text, useTheme} from 'react-native-paper';

// Styles
import StylesVariables from './../../config/Styles/app.style';
const TextArea = ({
  label = '',
  helper = '  ',
  value,
  onChangeText,
  type = 'primary',
  placeholder,
  maxLength = 100,
  keyboardType,
  disabled,
}) => {
  const {dark} = useTheme();
  const {ligthTextInputs, darkTextInputs} = StylesVariables;
  const palette = !dark ? {
    primary: ligthTextInputs.primary,
    secondary: ligthTextInputs.secondary,
    third: ligthTextInputs.third,
    danger: ligthTextInputs.danger,
  } : {
    primary: darkTextInputs.primary,
    secondary: darkTextInputs.secondary,
    third: darkTextInputs.third,
    danger: darkTextInputs.danger,
  };
  return (
    <View
      style={styles.container}
    >
      <Text
        style={styles.label}
      >
        {label}
      </Text>
      <InputText
        placeholder={placeholder}
        mode='outlined'
        value={value}
        onChangeText={onChangeText}
        outlineColor={
          palette[type].borderColor
        }
        activeOutlineColor={
          palette[type].borderColor
        }
        maxLength={maxLength}
        keyboardType={keyboardType}
        style={styles.textInput}
        disabled={disabled}
        multiline={true}
      />
      <Text
        style={[
          styles.helper,
          {
            color: palette[type].borderColor,
          },
        ]}
      >
        {helper}
      </Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    width: '100%',
  },
  label: {
    fontFamily: 'Regular',
    marginTop:
      StylesVariables.spacing * StylesVariables.responsiveHeightMulti,
    fontSize: 12 * StylesVariables.responsiveHeightMulti,
    color: StylesVariables.Asteroide80p,
  },
  textInput: {
    height: 92 * StylesVariables.responsiveHeightMulti,
    fontSize: 16 * StylesVariables.responsiveHeightMulti,
    backgroundColor: 'white',
  },
  helper: {
    fontFamily: 'Italic',
    fontSize: 10 * StylesVariables.responsiveHeightMulti,
    marginBottom:
      StylesVariables.spacing / 2 * StylesVariables.responsiveHeightMulti,
  },
});

TextArea.propTypes = {
  label: PropTypes.string,
  value: PropTypes.string,
  onChangeText: PropTypes.func,
  type: PropTypes.string,
  helper: PropTypes.string,
  placeholder: PropTypes.string,
  maxLength: PropTypes.number,
  keyboardType: PropTypes.string,
  disabled: PropTypes.bool,
};

export {TextArea};
