import {Button} from './Button';
import PropTypes from 'prop-types';
import React from 'react';
import {Modal, StyleSheet, TouchableOpacity, View} from 'react-native';
import {Text} from 'react-native-paper';

// Styles
import StylesVariables from './../../config/Styles/app.style';

// Icons
import {Ionicons, AntDesign} from '@expo/vector-icons';
const ModalAlert = ({isVissible, onClose, buttons, title, message}) => {
  return (
    <Modal
      visible={isVissible}
      transparent
      onRequestClose={onClose}
    >
      <View
        style={styles.blackScreen}
      />
      <View
        style={styles.container}
      >
        <View
          style={styles.modal}
        >
          <View
            style={[styles.row]}
          >
            <View
              style={{flex: 1}}
            >
              <Ionicons
                name="alert-circle"
                size={28 * StylesVariables.responsiveHeightMulti}
                color={StylesVariables.Warning}
                style={styles.iconWarning}
              />
            </View>
            <View
              style={{flex: 1, alignItems: 'flex-end'}}
            >
              <TouchableOpacity
                onPress={() => onClose()}
              >
                <AntDesign
                  name="close"
                  size={24 * StylesVariables.responsiveHeightMulti}
                  color={StylesVariables.Asteroide40p}
                />
              </TouchableOpacity>
            </View>
          </View>
          <View
            style={styles.textContainer}
          >
            <Text
              style={styles.title}
            >
              {title}
            </Text>
            <Text
              style={styles.message}
            >
              {message}
            </Text>
          </View>
          <View
            style={styles.rowButtons}
          >
            {
              buttons.map((item, index) => {
                return (
                  <View
                    key={index}
                    style={styles.buttonContainer}
                  >
                    <Button
                      title={item.text}
                      onPress={item.action}
                      mode={item.mode}
                      fullWidth={true}
                    />
                  </View>
                );
              })
            }
          </View>
        </View>
      </View>
    </Modal>
  );
};

ModalAlert.propTypes = {
  isVissible: PropTypes.bool,
  onClose: PropTypes.func,
  buttons: PropTypes.array,
  title: PropTypes.string,
  message: PropTypes.string,
};

const styles = StyleSheet.create({
  blackScreen: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    backgroundColor: 'black',
    opacity: .3,
  },
  container: {
    flex: 1,
    padding:
      StylesVariables.spacing * 2 * StylesVariables.responsiveHeightMulti,
    alignItems: 'center',
    justifyContent: 'center',
  },
  modal: {
    width: '100%',
    borderRadius: 15 * StylesVariables.responsiveHeightMulti,
    backgroundColor: StylesVariables.Blanco,
    padding: 10 * StylesVariables.responsiveHeightMulti,
  },
  row: {
    width: '100%',
    flexDirection: 'row',
  },
  title: {
    fontFamily: 'NunitoBold',
    fontSize: 20,
    color: StylesVariables.Asteroide,
  },
  message: {
    fontFamily: 'Nunito',
    color: StylesVariables.Asteroide80p,
  },
  textContainer: {
    width: '100%',
    alignItems: 'center',
    bottom: 10,
  },
  rowButtons: {
    width: '100%',
    alignItems: 'center',
    justifyContent: 'center',
    flexWrap: 'wrap',
    flexDirection: 'row',
  },
  buttonContainer: {
    flex: 1,
    padding: 4 * StylesVariables.responsiveHeightMulti,
  },
  iconWarning: {
    position: 'absolute',
    top: 13 * StylesVariables.responsiveHeightMulti,
  },
});
export {ModalAlert};
