import {Background, Button} from './../../components/UI';
import {Card, Subheading, Text, Title, useTheme} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useState} from 'react';
import {TextInput} from './../../components/UI';
import {TouchableOpacity, View} from 'react-native';
import {
  GoogleReCaptchaProvider,
  GoogleReCaptcha,
  useGoogleReCaptcha,
} from 'react-google-recaptcha-v3';
// Styles
import {authStyles} from './auth.styles';

// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import ShopClass from './../../services/Shop.class';

// Components
import Logo from './../../components/Logo';

const WatshappRegisterScreen = ({navigation}) => {
  const [number, setNumber] = useState('');
  const [validatePrevNumber, setValidatePrevNumber] = useState('');
  const dispach = useDispatch();
  const [errorNumber, setErrorNumber] = useState(' ');
  const validateNumber = (text) => {
    let newText = '';
    const numbers = '0123456789';

    for (let i = 0; i < text.length; i++) {
      if (numbers.indexOf(text[i]) > -1 ) {
        newText = newText + text[i];
      } else {
        setErrorNumber('Este campo solo admite números');
      };
    };
    if (newText !== number) {
      setErrorNumber(' ');
    };
    setNumber(newText);
  };

  const validNumberChangeScreen = async (text) => {
    if (text.length == 0) {
      setErrorNumber('Este campo es obligatorio');
      return;
    }
    if (
      text.length < 10
    ) {
      setErrorNumber('Porfavor introduzca un número valido');
      return;
    };
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      const shopClass = new ShopClass();
      /* Se comenta hasta tener el nuevo otp
      let name = await shopClass.getShopByNumber(text);
      if (name) {
        name = name.shopName;
      } else {
        name = 'Nuevo usuario';
      };
      */
      if (text !== validatePrevNumber) {
        await shopClass.messageTramit('52'+text);
        await shopClass.sendMessage('52'+text);
      };
      setValidatePrevNumber(text);
      navigation.navigate('VerificationCode', {
        number: number,
      });
    } catch (e) {
      dispach({
        message: {
          show: true,
          message: 'En este momento no podemos procesar su solicitud',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    };
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };
  const ReComponent = () => {
    const {executeRecaptcha} = useGoogleReCaptcha();

    const handleSubmit = async (data) => {
      try {
        const newToken = executeRecaptcha('MS_Pyme_DatosEmpresa');
        validNumberChangeScreen(number);
      } catch (err) {
        throw new Error('Token error');
      }
    };

    return (
      <View
        style={{
          marginVertical: 4,
        }}
      >
        <GoogleReCaptcha onVerify={() => {}} />
        <Button
          id={ number.length == 10 ? 'phoneNumber_whatsapp_button' : null}
          uppercase={false}
          onPress={handleSubmit}
          title='Ingresar'
          mode='buttonsGColors'
          fullWidth={true}
          category={'button'}
          action={'click'}
        />
      </View>
    );
  };
  return (
    <View
      style={[
        authStyles.interiorContainer,
        {
          backgroundColor: 'transparent',
          alignItems: 'center',
        },
      ]}
    >
      <Background/>
      <Card
        style={authStyles.codeContainer}
      >
        <Logo/>
        <View
          style={authStyles.titleContainer}
        >
          <Title
            style={authStyles.titleCards}
          >
            Identifícate
          </Title>
          <Subheading
            style={authStyles.titleSubCard}
          >
            Ingresa un número con WhatsApp
          </Subheading>
        </View>
        <View
          style={authStyles.row}
        >
          <TextInput
            label='Teléfono con WhatsApp'
            placeholder='Ej. 559900333'
            value={number}
            onChangeText={(value) => {
              validateNumber(value);
            }}
            type={errorNumber == ' ' ? 'primary' : 'danger'}
            maxLength={10}
            keyboardType={'numeric'}
            helper={errorNumber == ' ' ? 'Ingresa 10 dígitos' : errorNumber}
          />
        </View>
        <GoogleReCaptchaProvider
          reCaptchaKey="6LdVieweAAAAAF-PznPEeLiWPva8yoq59zWNWW6f"
        >
          <ReComponent/>
        </GoogleReCaptchaProvider>
        <View
          style={[
            authStyles.row,
            {flexDirection: 'column', alignItems: 'flex-start'},
          ]}
        >
          <Text
            style={authStyles.termsStyles}
          >
            Al continuar estarás aceptando los
          </Text>
          <TouchableOpacity
            onPress={() => navigation.navigate('Termns')}
          >
            <Text
              style={authStyles.termsTouchableText}
            >
              Términos y condiciones.
            </Text>
          </TouchableOpacity>
        </View>
      </Card>
    </View>
  );
};

WatshappRegisterScreen.propTypes = {
  navigation: PropTypes.any,
};

export default WatshappRegisterScreen;
