import Constants from 'expo-constants';
import GeocodingClass from '../../config/utils/GeolocationFunctions';
import {
  GoogleMap,
  Marker,
  withScriptjs,
  withGoogleMap,
  DirectionsRenderer,
} from 'react-google-maps';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';
import {Modal, StyleSheet, View} from 'react-native';
import GestureRecognizer from 'react-native-swipe-gestures';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const apiKey = Constants.manifest.extra.geoApi;

const Mapp = ({
  latitudeUser,
  longitudeUser,
  latitudeStore,
  longitudeStore,
}) => {
  const [directions, setDirections] = useState('');
  useEffect(() => {
    const directionsService = new google.maps.DirectionsService();
    directionsService.route(
        {
          origin: new google.maps.LatLng(latitudeUser, longitudeUser),
          destination: new google.maps.LatLng(latitudeStore, longitudeStore),
          travelMode: 'DRIVING',
        },
        (result, status) => {
          if (status === google.maps.DirectionsStatus.OK) {
            setDirections(result);
          } else {
            console.error(`error fetching directions ${result}`);
          }
        },
    );
  }, []);
  return (
    <GoogleMap
      center={{lat: latitudeUser, lng: longitudeUser}}
    >
      <Marker
        position={{
          lat: latitudeStore,
          lng: longitudeStore,
        }}
        icon={{
          url: require('../../../assets/icons/storeLocation.png'),
          scaledSize: new google.maps.Size(50, 50),
        }}
      />
      <Marker
        position={{
          lat: latitudeUser,
          lng: longitudeUser,
        }}
      />
      {directions && <DirectionsRenderer directions={directions} />}
    </GoogleMap>
  );
};
const WrapperMap = withScriptjs(withGoogleMap(Mapp));
const MapModal = ({
  isOpen,
  closeModal,
  latitudeStore,
  longitudeStore,
}) => {
  useEffect(() => {
    getCurrentLocation();
  }, []);
  const [latLng, setLatLng] = useState(false);
  const getCurrentLocation = async () => {
    try {
      const coords = await GeocodingClass.getCurrentPosition();
      setLatLng({
        latitude: coords.latitude,
        longitude: coords.longitude,
      });
    } catch (e) {
      alert('Necesitamos tu permiso para esta funcion');
      console.log(e);
    };
  };

  if (!latLng) {
    return null;
  };

  return (
    <Modal
      visible={isOpen}
      transparent={true}
      swipeDirection="down"
      onSwipeComplete={() => closeModal()}
    >
      <View
        style={styles.blackScreen}
      >
      </View>
      <GestureRecognizer
        style={styles.bottomButtonContainer}
        onSwipeDown={ () => closeModal() }
      >
        <View
          style={styles.formContainer}
        >
          <WrapperMap
            loadingElement={<div style={{height: `100%`}} />}
            googleMapURL={
              'https://maps.googleapis.com/maps/api/js?key='+apiKey+'&libraries=geometry,drawing,places,directions'
            }
            containerElement={<div style={{
              height: `400px`,
              width: '100%',
              display: 'flex',
              flexDirection: 'column-reverse',
            }}/>}
            mapElement={<div style={{height: `100%`}}/>}
            latitudeUser={latLng.latitude}
            longitudeUser={latLng.longitude}
            latitudeStore={latitudeStore}
            longitudeStore={longitudeStore}
          >
          </WrapperMap>
        </View>
      </GestureRecognizer>
    </Modal>
  );
};

MapModal.propTypes = {
  isOpen: PropTypes.bool,
  closeModal: PropTypes.func,
  latitudeStore: PropTypes.number,
  longitudeStore: PropTypes.number,
};

Mapp.propTypes = {
  latitudeUser: PropTypes.number,
  longitudeUser: PropTypes.number,
  latitudeStore: PropTypes.number,
  longitudeStore: PropTypes.number,
};
const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    justifyContent: 'flex-end',
  },
  blackScreen: {
    width: '100%',
    height: '100%',
    backgroundColor: 'black',
    opacity: 0.5,
    position: 'absolute',
  },
  bottomButtonContainer: {
    justifyContent: 'flex-end',
    flex: 1,
    width: '100%',
  },
  title: {
    fontFamily: 'NunitoBold',
  },
  formContainer: {
    borderTopStartRadius: 30,
    borderTopEndRadius: 30,
    backgroundColor: StylesVariables.Blanco,
    alignItems: 'center',
    overflow: 'hidden',
    height: 400,
  },
});

export {MapModal};
