import {Banner} from 'react-native-paper';
import PropTypes from 'prop-types';
import React from 'react';

// Styles
import StylesVariables from '../config/Styles/app.style';

// Icons
import {AntDesign} from '@expo/vector-icons';

const ModalMessage = ({isVisible, message, buttons, type}) => {
  const messageStatus = () => {
    switch (type) {
      case 'S':
        return (
          <AntDesign
            name="checkcircleo"
            size={25 * StylesVariables.responsiveHeightMulti}
            color={StylesVariables.Kometa}
          />
        );
      case 'N':
        return (
          <AntDesign
            name="closecircleo"
            size={25 * StylesVariables.responsiveHeightMulti}
            color={StylesVariables.Kometa}
          />
        );
    }
  };

  return (
    <Banner
      actions={
        buttons
      }
      visible={isVisible}
      icon={({size}) => {
        return messageStatus();
      }}
    >
      {message}
    </Banner>
  );
};

ModalMessage.propTypes = {
  isVisible: PropTypes.any,
  message: PropTypes.string,
  buttons: PropTypes.array,
  type: PropTypes.string,
};

export default ModalMessage;

