import {Animated, Image, Platform, View} from 'react-native';
import {Background, Button} from './../../components/UI';
import {Text} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useRef} from 'react';

// Styles
import {authStyles} from './../auth/auth.styles';

// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT, AUTH_USER} from '../../redux/constants';
import {useDispatch} from 'react-redux';
const WelcomeStore = ({navigation, route}) => {
  const dispach = useDispatch();
  useEffect(() => {
    setTimeout(() => {
      fadeIn();
    }, 1000);
  }, []);

  const fadeAnim = useRef(new Animated.Value(0)).current;
  const moveBottom = useRef(new Animated.Value(50)).current;

  const fadeIn = () => {
    Animated.timing(moveBottom, {
      toValue: 0,
      duration: 1000,
    }).start();
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 1000,
    }).start();
  };

  const goRoute = async (isnew) => {
    const {info} = route.params;
    if (Platform.OS === 'web') {
      localStorage.setItem('kometa-token', info.phoneNumber);
    } else {
      await AsyncStorage.setItem('kometa-token', info.phoneNumber);
    };
    dispach({
      userInfo: {
        number: info.phoneNumber,
        userInfo: {
          ...info,
          id: info.id,
          newStore: isnew,
        },
      },
      type: AUTH_USER,
    });
  };
  return (
    <View
      style={authStyles.interiorContainer}
    >
      <Background/>
      <View
        style={authStyles.responsiveContainer}
      >
        <Animated.View
          style={{
            opacity: fadeAnim,
            bottom: moveBottom,
          }}
        >
          <Image
            source={require('../../../assets/welcomeImage.png')}
            style={{
              width: 300,
              height: 300,
            }}
            resizeMode='contain'
          />
          <Text
            style={[
              authStyles.cardTitle,
              {color: 'black'},
            ]}
          >
            ¡Te damos la bienvenida!
          </Text>
          <Text
            style={[
              authStyles.textWelcome,
            ]}
          >
            Comparte tu tienda y aumenta 30%
            tus ventas
          </Text>
          <View
            style={authStyles.buttonContainer}
          >
            <Button
              id={'welcome_screen_button'}
              uppercase={false}
              onPress={() => {
                goRoute(true);
              }}
              title='Crear mi tienda'
              mode={'buttonsGColors'}
              textSty={authStyles.textButton}
              fullWidth={true}
            />
          </View>
          <View
            style={authStyles.buttonContainer}
          >
            <Button
              uppercase={false}
              onPress={() => {
                goRoute(false);
              }}
              title='Mis recursos'
              mode={'buttonsGOutlineColors'}
              textSty={authStyles.textButton}
              fullWidth={true}
            />
          </View>
        </Animated.View>
      </View>
    </View>
  );
};

WelcomeStore.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};

export default WelcomeStore;
