import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, TouchableOpacity, View} from 'react-native';
import {Text} from 'react-native-paper';

// Styles
import StylesVariables from '../../config/Styles/app.style';

/*
  interface tabModel: {;
    tabActiveColor: string;
    tabInactiveColor: string;
    optionText: string;
  }
  tabModel : tabModel[]
*/
const Tabs = ({tabs, current, changeTab, disableCurrent}) => {
  return (
    <View
      style={styles.container}
    >
      {
        tabs.map((tab, index) => {
          return (
            <TouchableOpacity
              key={index}
              style={{flex: 1}}
              onPress={() => {
                if (disableCurrent !== index) {
                  changeTab(index);
                };
              }}
            >
              <View
                style={[
                  styles.tabContainer,
                  {
                    borderBottomColor:
                    current == index ?
                    tab.activeColor : tab.inactiveColor,
                  },
                ]}
              >
                {tab.icon()}
                <Text
                  style={[
                    styles.text,
                    {
                      color:
                        current == index ?
                        tab.activeColor : tab.inactiveColor,
                    },
                  ]}
                >
                  {' '+tab.optionText}
                </Text>
              </View>
            </TouchableOpacity>
          );
        })
      }
    </View>
  );
};

Tabs.propTypes = {
  tabs: PropTypes.array,
  current: PropTypes.number,
  changeTab: PropTypes.func,
  disableCurrent: PropTypes.number,
};

const styles = StyleSheet.create({
  container: {
    width: '100%',
    flexDirection: 'row',
    alignItems: 'center',
    marginVertical: 8 * StylesVariables.responsiveHeightMulti,
  },
  tabContainer: {
    flex: 1,
    padding: 16 * StylesVariables.responsiveHeightMulti,
    borderBottomWidth: 1,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  text: {
    fontFamily: 'NunitoBold',
    fontSize: 16,
  },
});
export {Tabs};
