import {
  AddImage,
  Background,
  Button,
  ModalAlert,
  Navbar,
  Tabs,
  TextArea,
  TextInput,
  Select,
} from './../../components/UI';
import {
  Divider,
  Title,
  Text,
} from 'react-native-paper';
import {
  Platform,
  ScrollView,
  View,
  Image,
} from 'react-native';
import MapView from '../../components/Maps.screen';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';

// Styles
import {newMarketStyles as styles} from './Bussines.styles';

// Utils
import GeocodingClass from '../../config/utils/GeolocationFunctions';
import TakeImageClass from '../../config/utils/ImagePreparison';

// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT, AUTH_USER} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import ShopClass from '../../services/Shop.class';

// Styles
import StylesVariables from '../../config/Styles/app.style';
const NewScreen = ({navigation, route}) => {
  const tabsModel = [
    {
      activeColor: StylesVariables.Kometa,
      inactiveColor: StylesVariables.Asteroide60p,
      optionText: 'Información',
      icon: () => {
        return (
          <Image
            source={require('../../../assets/icons/storeTab.png')}
            style={{
              width: 16,
              height: 16,
              tintColor: '#00D6AC',
            }}
          />
        );
      },
    },
    {
      activeColor: StylesVariables.Kometa,
      inactiveColor: StylesVariables.Asteroide60p,
      optionText: 'Entregas',
      icon: () => {
        return (
          <Image
            source={require('../../../assets/icons/deliveryTab.png')}
            style={{
              width: 14,
              height: 14,
              tintColor: '#9FABA9',
            }}
          />
        );
      },
    },
  ];
  const dispach = useDispatch();
  const [form, setForm] = useState({
    image: null,
    name: '',
    description: '',
  });
  const [formHomolog, setFormHomolog] = useState({});
  const [locationStack, setLocationStack] = useState({
    address: '',
    latitude: 20.387298,
    longitude: -99.985317,
  });
  const [list, setList] = useState({
    value: '',
    list: [
    ],
    selectedList: [],
    error: '',
  });
  const [localModal, setLocalModal] = useState(false);
  const buttonsModal = [
    {
      text: 'Sí, continuar',
      action: () => changeTab(),
      mode: 'buttonsOutlineColors',
    },
    {
      text: 'Regresar',
      action: () => setLocalModal(false),
      mode: 'buttonsColors',
    },
  ];
  const changeForm = (key, value) => {
    setForm({
      ...form,
      [key]: value,
    });
  };
  const [errorName, setErrorName] = useState(' ');
  const [errorDescription, setErrorDescription] = useState(' ');
  const [errorAddress, setErrorAddress] = useState(' ');
  const [errorCategorys, setErrorCategorys] = useState(' ');

  useEffect(() => {
    const getData = navigation.addListener('focus', () => {
      const {name} = route;
      getDataStore();
    });

    return getData;
  }, [navigation]);

  const getImage = async () => {
    const image = await TakeImageClass.getImageByDevice();
    if (image) {
      changeForm('image', image);
    };
  };
  const removeImage = async () => {
    changeForm('image', null);
  };
  const nextStep = async () => {
    const formu = {
      ...form,
      ...locationStack,
    };
    formu.category = list.selectedList;
    let newImage = '';
    if (formu.image) {
      newImage = await TakeImageClass.formatImageArr([formu.image]);
      newImage = newImage[0];
    }
    formu.image = newImage;
    const validate = Object.keys(formu);
    const errors = [];
    const errorMessages = 'Debes ingresar esta información';
    for (const value of validate) {
      if (
        value !== 'image' &&
        !formu[value] &&
        value !== 'homeDelivery' &&
        value !== 'storeDelivery'
      ) {
        errors.push(value);
      };
    };
    if (errors.length) {
      if (errors.includes('name')) {
        setErrorName(errorMessages);
      };
      if (errors.includes('description')) {
        setErrorDescription(errorMessages);
      };
      if (
        formu.address == '' ||
        !formu.address
      ) {
        setErrorAddress(errorMessages);
      };
      dispach({
        layout: {
          layout: false,
        },
        type: RENDER_LAYOUT,
      });
      return;
    };
    if (!list.value) {
      setErrorCategorys(errorMessages);
      return;
    };
    if (route.name == 'AlterMarket') {
      alterStore();
      return;
    };
    navigation.navigate('StepTwo', {
      info: {
        ...formu,
        number: formu.number,
        category: formu.category[0],
      },
    });
  };

  const getCurrentLocation = async () => {
    try {
      const latLng = await GeocodingClass.getCurrentPosition();
      const address =
        await GeocodingClass
            .reverseGeoCoding(latLng.latitude, latLng.longitude);
      setLocationStack({
        latitude: latLng.latitude,
        longitude: latLng.longitude,
        address: address,
      });
    } catch (e) {
      console.log(e);
    };
  };

  const getLocationByMap = async (lat, lng) => {
    const address =
      await GeocodingClass.reverseGeoCoding(lat, lng);
    setLocationStack({
      latitude: lat,
      longitude: lng,
      address: address,
    });
  };

  // AlterActions
  const getDataStore = async () => {
    const {storeId} = route.params;
    const shop = new ShopClass();
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      const data = await shop.getShopById(storeId);
      const values = await shop.getCategories();
      const newArr = values.map((item) => {
        return {
          _id: item.id,
          value: item.description,
        };
      });
      let newImage;
      if (data.image) {
        newImage =
          await TakeImageClass.getEmergencyFoundImg(data.image);
      } else {
        newImage = null;
      };
      setForm({
        image: newImage,
        name: data.shopName,
        description: data.shopDescription,
        storeDelivery: data.storeDelivery,
        homeDelivery: data.homeDelivery,
        id: data.id,
        number: data.phoneNumber,
      });
      setLocationStack({
        address: data.address,
        latitude: parseFloat(data.latitude),
        longitude: parseFloat(data.longitude),
      });
      setFormHomolog({
        image: newImage,
        name: data.shopName,
        description: data.shopDescription,
        storeDelivery: data.storeDelivery,
        homeDelivery: data.homeDelivery,
        id: data.id,
        number: data.phoneNumber,
        address: data.address,
        latitude: parseFloat(data.latitude),
        longitude: parseFloat(data.longitude),
        category: data.category ? {
          _id: data.category.id,
          value: data.category.description,
        } : null,
      });
      setList({
        list:
          newArr.sort((a, b) => a.value.localeCompare(b.value)),
        value: data.category ? {
          label: data.category.description,
          value: data.category.id,
        } : null,
        selectedList: data.category ? [
          {
            _id: data.category.id,
            value: data.category.description,
          },
        ] : [],
        error: '',
      });
    } catch (e) {
      console.log(e);
      dispach({
        message: {
          show: true,
          message: 'En este momento no podemos procesar tu solicitud',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
      navigation.goBack();
    };
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const changeTab = async (index) => {
    const formu = formHomolog;
    setLocalModal(false);
    navigation.navigate('AlterStepTwo', {
      info: {
        ...formu,
      },
    });
  };

  const alterStore = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    const {storeId} = route.params;
    let img = '';
    if (form.image) {
      img = await TakeImageClass.formatImageArr([form.image]);
      img = img[0];
    };
    try {
      const shopClass = new ShopClass();
      const userData = {
        shopName: form.name,
        phoneNumber: form.number,
        address: locationStack.address,
        latitude: locationStack.latitude,
        longitude: locationStack.longitude,
        shopDescription: form.description,
        category: list.selectedList[0]._id,
        image: img,
        storeDelivery: form.storeDelivery,
        homeDelivery: form.homeDelivery,
      };

      await shopClass.alterShop(
          userData,
          storeId,
      );
      const userInfo = await shopClass.getShopById(storeId);

      dispach({
        userInfo: {
          number: userData.phoneNumber,
          userInfo: userInfo,
        },
        type: AUTH_USER,
      });
      navigation.navigate('HomeStore');
      dispach({
        message: {
          show: true,
          message: 'Tienda Editada',
          status: 'S',
        },
        type: RENDER_MESSAGE,
      });
    } catch (e) {
      console.log(e);
      dispach({
        message: {
          show: true,
          message: 'En este momento no podemos procesar tu solicitud',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
      dispach({
        layout: {
          layout: false,
        },
        type: RENDER_LAYOUT,
      });
    };
  };
  return (
    <View
      style={[
        styles.container,
      ]}
    >
      <Background/>
      <Navbar
        navigation={navigation}
      />
      <ScrollView
        style={styles.scrollContainer}
        keyboardDismissMode={'interactive'}
        keyboardShouldPersistTaps={'handled'}
        contentContainerStyle={styles.contentContainer}
      >
        <View
          style={styles.interiorContainer}
        >
          <View style={[
            styles.row,
            {flexDirection: 'column'},
          ]}>
            <Title
              style={styles.titlesStyle}
            >
              {route.name !== 'AlterMarket' ? 'Tienda' : 'Editar Tienda'}
            </Title>
            {
              route.name !== 'AlterMarket' &&
              <Text
                style={styles.subTitleStyle}
              >
                Comienza contándole a tus clientes de tu negocio
              </Text>
            }
          </View>
          {
            route.name == 'AlterMarket' &&
              <Tabs
                tabs={tabsModel}
                current={0}
                changeTab={() => {
                  setLocalModal(true);
                }}
                disableCurrent={0}
              />
          }
          <View
            style={[
              styles.row,
            ]}
          >
            <Text
              style={[styles.subTitleStyle, {fontSize: 15}]}
            >
              Foto de tienda / Logotipo
            </Text>
            <AddImage
              image={form.image}
              title={
                form.image?.title ?
                form.image?.title :
                'Logotipo'
              }
              onAddImage={getImage}
              removeImage={removeImage}
            />
          </View>
          <View
            style={styles.row}
          >
            <TextInput
              value={form.name}
              label='Nombre de tu tienda*'
              placeholder='Ej: Tacos Casablanca'
              onChangeText={(value) => {
                if (
                  value.match(/^[A-Za-zÁÉÍÓÚáéíóúñÑ0-9 ]+$/g) ||
                  value.length === 0
                ) {
                  setErrorName(' ');
                  changeForm('name', value);
                  return;
                };
                setErrorName('Este campo solo acepta letras y numeros');
              }}
              helper={errorName}
              type={errorName == ' ' ? 'primary' : 'danger'}
              maxLength={30}
            />
          </View>
          <View
            style={styles.row}
          >
            <TextArea
              value={form.description}
              label='Describe tu tienda*'
              placeholder='Ej: Tacos de carnitas'
              onChangeText={(value) => {
                setErrorDescription(' ');
                changeForm('description', value);
              }}
              maxLength={200}
              helper={errorDescription}
              type={errorDescription == ' ' ? 'primary' : 'danger'}
            />
          </View>
          <View
            style={styles.row}
          >
            <Select
              activeOutlineColor='#828282'
              outlineColor='#828282'
              label="Categoria*"
              list={list}
              setList={(v) => {
                setErrorCategorys(' ');
                setList(v);
              }}
              type={errorCategorys == ' ' ? 'primary' : 'danger'}
              helper={errorCategorys}
            />
          </View>
          <View
            style={{padding: 10}}
          />
          <View
            style={{zIndex: -10}}
          >
            <View
              style={styles.row}
            >
              <Divider/>
            </View>
            <View
              style={{padding: 10}}
            />
            <View
              style={[styles.row, {zIndex: -10}]}
            >
              <Button
                title='Usar mi ubicación actual'
                onPress={getCurrentLocation}
                mode={'buttonsGOutlineColors'}
                fullWidth={true}
                out={true}
              />
            </View>
            <View
              style={[
                styles.row,
                {alignItems: 'center'},
              ]}
            >
              <Text
                style={{fontSize: 25}}
              >
                O
              </Text>
            </View>
            <View
              style={[styles.row, {top: 5}]}
            >
              <Text
                style={styles.addLocationText}
              >
                Ingresa dirección *
              </Text>
            </View>
            <View
              style={styles.mapContainer}
            >
              <MapView
                changeAddressText={(value) =>{
                  setErrorAddress(' ');
                  setLocationStack({
                    ...locationStack,
                    address: value,
                  });
                }}
                address={locationStack.address}
                latitude={locationStack.latitude}
                longitude={locationStack.longitude}
                changeAddress={getLocationByMap}
                editable={true}
                errorMessage={errorAddress}
              />
            </View>
            <View
              style={styles.row}
            >
              <View
                style={{padding: 10}}
              />
              <Button
                id={ route.name !== 'AlterMarket' ? 'add_market' : null }
                onPress={nextStep}
                fullWidth={true}
                mode={'buttonsGColors'}
                title={
                  route.name !== 'AlterMarket' ? 'Siguiente':
                  'Actualizar Tienda'
                }
              />
            </View>
            {
              route.name == 'AlterMarket' &&
              <View
                style={styles.row}
              >
                <View
                  style={{padding: 10}}
                />
                <Button
                  onPress={() => navigation.navigate('HomeStore')}
                  fullWidth={true}
                  mode={'buttonsGOutlineColors'}
                  title={
                    'Cancelar'
                  }
                />
              </View>
            }
          </View>
          <View
            style={{padding: 10}}
          />
        </View>
      </ScrollView>
      <ModalAlert
        isVissible={localModal}
        title={'¿Estás seguro de continuar?'}
        message={'Los cambios que realizaste se podrían perder'}
        buttons={buttonsModal}
        onClose={() => setLocalModal(false)}
      />
    </View>
  );
};
NewScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};
export default NewScreen;
