import axios from 'axios';
import Constants from 'expo-constants';
import * as Location from 'expo-location';
const apiKey = Constants.manifest.extra.geoApi;
/**
 * Esta clase encapsula todo lo que tiene que
 * ver con geolocation. Pedir permisos, usar
 * geolocacion inversa, actual, etc
 */
class GeolocationFunctions {
  /**
   * getCurrentPosition se usa para optener la
   * latitude y la longitude del dispositivo
   * @return {Promise}
  */
  getCurrentPosition() {
    return new Promise(async (resolve, reject) => {
      try {
        const {status} = await Location.requestForegroundPermissionsAsync();
        if (status !== 'granted') {
          reject(
              new Error('Necesitamos permiso para acceder a su ubicacion'),
          );
          return;
        }
        const location = await Location.getCurrentPositionAsync();
        resolve({
          latitude: location.coords.latitude,
          longitude: location.coords.longitude,
        });
      } catch (e) {
        reject(JSON.stringify(e));
      };
    });
  };
  /**
  * @parametros {string} direccion
  * Represents a parametros.
  * Geocoding convierte un address en un geocoding
  * @param {string} address es la direccion a convertir
  * @return {Promise}
  */
  normalGeoCoding(address) {
    return new Promise(async (resolve, reject) => {
      const formattedAdress =
        address.replace(/[^\w\s]/gi, '').replace(/\s/g, '+');
      axios.get(
          `https://maps.googleapis.com/maps/api/geocode/json?address=${formattedAdress}&key=${apiKey}`,
      ).then( (response) => {
        if (response.data.status == 'OK') {
          resolve(response.data.results[0].geometry.location);
        } else {
          reject(
              new Error('No existe una ubicacion'),
          );
        };
      }).catch( (err) => {
        reject(
            new Error(err.stringify(err)),
        );
      });
    });
  };
  /**
  * @parametros {number} latitude
  * @parametros {number} longitude
  * Represents a parametros.
  * Reverse Geocoding convierte latitud y longitud
  * a formatedd Address
  * @param {number} latitude es la latitude
  * @param {number} longitude es la longitude
  * @return {Promise}
  */
  reverseGeoCoding(latitude, longitude) {
    return new Promise(async (resolve, reject) => {
      axios.get(
          `https://maps.googleapis.com/maps/api/geocode/json?latlng=
          ${latitude},${longitude}&key=${apiKey}`,
      ).then( (response) => {
        if (response.data.status == 'OK') {
          resolve(response.data.results[0].formatted_address);
        } else {
          reject(
              new Error('No existe una ubicacion'),
          );
        };
      }).catch( (err) => {
        reject(
            new Error(err.stringify(err)),
        );
      });
    });
  };
  /**
  * @parametros {number} lat1
  * @parametros {number} long1
  * @parametros {number} lat2
  * @parametros {number} long2
  * Represents a parametros.
  * getRadiusKilometer te regresa el radio
  * en el que algo se encuentra
  * @param {number} lat1 es la latitude del punto A
  * @param {number} lon1 es la longitude del punto A
  * @param {number} lat2 es la latitude del punto B
  * @param {number} lon2 es la longitude del punto B
  * @return {number}
  */
  getRadiusKilometer(lat1, lon1, lat2, lon2) {
    const toRad = (Value) => {
      return Value * Math.PI / 180;
    };
    const R = 6371; // km
    const dLat = toRad(lat2-lat1);
    const dLon = toRad(lon2-lon1);
    lat1 = toRad(lat1);
    lat2 = toRad(lat2);
    const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
      Math.sin(dLon/2) * Math.sin(dLon/2) * Math.cos(lat1) * Math.cos(lat2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
    const d = R * c;
    return d
  };
  /**
  * @parametros {string} ubicacion
  * Represents a parametros.
  * placesAutoCompleteInput recibe ciertos
  * parametros para posteriormente regresar
  * lugares que contengan lo escrito en el input
  * @param {string} input que contiene el input
  * @return {Promise}
  */
  placesAutoCompleteInput(input) {
    return new Promise(async (resolve) => {
      try {
        const places = await axios.get(
            `https://maps.googleapis.com/maps/api/place/autocomplete/json?input=${input}&language=es&types=geocode&key=${apiKey}`,
        );
        resolve(places);
      } catch (e) {
        console.log(e);
        resolve([]);
      }
    });
  };
};

export default new GeolocationFunctions();
