const NewMarketStack = createStackNavigator();
const NewMarket = () => {
  return (
    <NewMarketStack.Navigator
      screenOptions={{headerShown: false}}
    >
      <NewMarketStack.Screen
        name='NewMarket'
        component={NewMarketScreen}
        options={{headerShown: false}}
      />
      <NewMarketStack.Screen
        name='StepTwo'
        component={StepTwoScreen}
        options={{headerShown: false}}
      />
      <NewMarketStack.Screen
        name='SuccesForm'
        component={SuccessFormScreen}
        options={{headerShown: false}}
      />
    </NewMarketStack.Navigator>
  );
};


const UserStack = createStackNavigator();
const User = () => {
  return (
    <UserStack.Navigator
      screenOptions={options}
      initialRouteName='Store'
    >
      <UserStack.Screen
        name='Store'
        component={HomeStore}
        options={{headerShown: false}}
      />
      <UserStack.Screen
        name='ProductDetails'
        component={ProductDetailsUser}
        options={{headerShown: false}}
      />
      <UserStack.Screen
        name='CarInfo'
        component={CarInfo}
        options={{headerShown: false}}
      />
    </UserStack.Navigator>
  );
};

const RootContainer = () => {
  const config = {
    screens: {
      Store: {
        path: 'mitienda/:idShop/:utm?',
      },
      ProductDetails: 'product/:productId',
      home: {
        path: 'home/:*?',
      },
      Termns: {
        path: 'Termns',
      },
      Privacy: {
        path: 'Privacy',
      },
    },
  };
  const linking = {
    config,
  };
  const {message} = useSelector(
      ( state ) => ({
        message: renderAndChange(state),
      }),
  );

  const {layout} = useSelector(
      (state) => ({
        layout: renderAndShow(state),
      }),
  );
  const {user} = useSelector(
      (state) => ({
        user: authAndDrive(state),
      }),
  );
  const dispach = useDispatch();
  useEffect(() => {
    checkLogin();
  }, []);
  const checkLogin = async () => {
    const pathname = window.location.pathname;
    if (
      pathname == '/' ||
      pathname === '/home' ||
      pathname.includes('Termns') ||
      pathname.includes('Privacy')
    ) {
      dispach({
        userInfo: {
          number: userInfo?.phoneNumber,
          userInfo: {},
          costumer: 'no_costumer',
        },
        type: AUTH_USER,
      });
      return;
    };
    let token = '';
    if (Platform.OS === 'web') {
      token = localStorage.getItem('kometa-token');
    } else {
      token = await AsyncStorage.getItem('kometa-token');
    };
    let userInfo = {};
    if (token) {
      const getInfo = new ShopClass();
      userInfo = await getInfo.getShopByNumber(token);
    };

    if (
      !pathname.includes('product') &&
      !pathname.includes('mitienda')
    ) {
      dispach({
        userInfo: {
          number: userInfo?.phoneNumber,
          userInfo,
          costumer: 'no_costumer',
        },
        type: AUTH_USER,
      });
      return;
    };

    if (pathname.includes('product')) {
      dispach({
        userInfo: {
          number: userInfo?.phoneNumber,
          userInfo,
          costumer: true,
        },
        type: AUTH_USER,
      });
      return;
    };

    let number;
    if (pathname.includes('mitienda')) {
      number = pathname.replace(/\//g, '');
      number = number.substring(number.length -5);
    };

    let costumer = true;
    if (number === userInfo?.idShop) {
      costumer = 'no_costumer';
    };
    dispach({
      userInfo: {
        number: userInfo?.phoneNumber,
        userInfo,
        costumer,
      },
      type: AUTH_USER,
    });
  };

  const closeMessage = () => {
    dispach({
      message: {
        show: false,
        message: message.message.message.message,
        status: message.message.message.status,
      },
      type: RENDER_MESSAGE,
    });
  };

  if (
    user.userInfo.user.number === '' &&
    !user.userInfo.user.costumer
  ) {
    return (
      <Loading/>
    );
  };

  const sessionType = () => {
    if (
      user.userInfo.user.costumer === true ||
      user.userInfo.user.costumer === false
    ) {
      return <User/>;
    };
    if (!user.userInfo.user.number) {
      return <Auth/>;
    };
    if (user.userInfo.user.number) {
      return (
        <>
          <App/>
        </>
      );
    };
  };

  return (
    <View
      style={{
        flex: 1,
        backgroundColor: 'transparent',
      }}
    >
      <View
        style={{
          flex: 1,
          overflow:
            layout.layout.layout.layout ? 'hidden' : 'visible',
        }}
      >
        <NavigationContainer linking={linking}>
          {sessionType()}
        </NavigationContainer>
      </View>
      <LoadingModal
        show={layout.layout.layout.layout}
      />
      <Modal
        isVisible={message.message.message.show}
        type={message.message.message.status}
        message={message.message.message.message}
        buttons={[
          {
            onPress: () => closeMessage(),
            label: 'Aceptar',
            color: StylesVariables.Kometa,
          },
        ]}
      />
    </View>
  );
};

export default RootContainer;
