import PropTypes from 'prop-types';
import React from 'react';
import {Platform, StyleSheet, View} from 'react-native';
import {Text, useTheme} from 'react-native-paper';
// Styles
import StylesVariables from './../../config/Styles/app.style';
import SelectWeb from 'react-select';

const Select = ({
  label = '',
  helper = ' ',
  type = 'primary',
  list,
  setList,
}) => {
  const {dark, colors} = useTheme();
  const {ligthTextInputs, darkTextInputs} = StylesVariables;
  const palette = !dark ? {
    primary: ligthTextInputs.primary,
    secondary: ligthTextInputs.secondary,
    third: ligthTextInputs.third,
    danger: ligthTextInputs.danger,
  } : {
    primary: darkTextInputs.primary,
    secondary: darkTextInputs.secondary,
    third: darkTextInputs.third,
    danger: darkTextInputs.danger,
  };

  const stylesWeb = {
    select: {
      height: 44 * StylesVariables.responsiveHeightMulti,
      border: `1px solid ${palette[type].borderColor}`,
      borderRadius: 5,
      color: StylesVariables.AsteroideDark,
      marginLeft: 0,
      fontSize: StylesVariables.textFontSize + 1,
      backgroundColor: colors.background,
      width: '100%',
    },
  };
  const customStyles = {
    container: (provided, state) => ({
      ...provided,
      ...stylesWeb.select,
      fontFamily: 'Regular',
      fontSize: 16 * StylesVariables.responsiveHeightMulti,
      marginTop: 5,
      backgroundColor: 'white',
      zIndex: -21,
    }),
    control: (provided, state) => ({
      ...provided,
      ...stylesWeb.select,
      fontFamily: 'Regular',
      fontSize: 16 * StylesVariables.responsiveHeightMulti,
      backgroundColor: 'white',
      zIndex: -21,
    }),
    input: (provided, state) => ({
      ...provided,
      zIndex: -21,
    }),
    placeholder: (provided, state) => ({
      ...provided,
      fontSize: 16 * StylesVariables.responsiveHeightMulti,
    }),
    menu: (provided, state) => ({
      ...provided,
      paddingTop: 25,
      zIndex: 100,
    }),
  };
  if (Platform.OS === 'web') {
    return (
      <View
        style={styles.container}
      >
        <Text
          style={[
            {...styles.label, color: palette[type].label},
          ]}
        >
          {label}
        </Text>
        <SelectWeb
          styles={customStyles}
          value={
            list.value ? list.value : null
          }
          onChange={(value) => {
            setList({
              ...list,
              value: {
                label: value.label,
                value: value.value,
              },
              selectedList: [{
                _id: value.value,
                label: value.label,
              }],
              error: '',
            });
          }}
          placeholder={'Selecciona una categoría'}
          getStyles={stylesWeb.select}
          options={list.list.map((item) => {
            return ({
              value: item._id,
              label: item.value,
            });
          })}
        />
        <Text
          style={[
            {
              ...styles.helper,
              color: palette[type].borderColor,
            },
          ]}
        >
          {helper}
        </Text>
      </View>
    );
  };
  /*
  return (
    <View
      style={styles.container}
    >
      <Text
        style={[
          {...styles.label, color: palette[type].label},
        ]}
      >
        {label}
      </Text>
      <PaperSelect
        activeOutlineColor={palette[type].borderColor}
        outlineColor={palette[type].borderColor}
        dialogStyle={{backgroundColor: colors.background}}
        checkboxUncheckedColor={palette[type].borderColor}
        checkboxColor={palette[type].borderColor}
        checkboxLabelStyle={{color: palette[type].label}}
        value={list.value}
        onSelection={(value) => {
          setList({
            ...list,
            value: value.text,
            selectedList: value.selectedList,
            error: '',
          });
        }}
        textInputHeight={40 * StylesVariables.responsiveHeightMulti}
        arrayList={[...list.list]}
        selectedArrayList={list.selectedList}
        errorText={list.error}
        multiEnable={false}
        searchStyle={{
          iconColor: 'red',
          borderColor: palette[type].borderColor,
          backgroundColor: colors.background,
        }}
        textInputMode='outlined'
        textInputBackgroundColor={colors.background}
      />
      <Text
        style={[
          {
            ...styles.helper,
            color: palette[type].label,
          },
        ]}
      >
        {helper}
      </Text>
    </View>
  );
  */
};

const styles = StyleSheet.create({
  container: {
    width: '100%',
  },
  label: {
    fontFamily: 'Regular',
    marginTop:
      StylesVariables.spacing * StylesVariables.responsiveHeightMulti,
    fontSize: 12 * StylesVariables.responsiveHeightMulti,
    color: StylesVariables.Asteroide80p,
  },
  textInput: {
    height: 43 * StylesVariables.responsiveHeightMulti,
  },
  helper: {
    fontFamily: 'Italic',
    fontSize: 8 * StylesVariables.responsiveHeightMulti,
    marginBottom:
      StylesVariables.spacing * StylesVariables.responsiveHeightMulti,
  },
});

Select.propTypes = {
  label: PropTypes.string,
  value: PropTypes.string,
  type: PropTypes.string,
  helper: PropTypes.string,
  list: PropTypes.any,
  setList: PropTypes.func,
};

export {Select};
