import AsyncStorage from '@react-native-async-storage/async-storage';
import {Background, Button} from '../../components/UI';
import {Animated, Platform, View} from 'react-native';
import {Text} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useRef} from 'react';

// Styles
import {authStyles} from './../auth/auth.styles';
import StylesVariables from './../../config/Styles/app.style';

// redux
import {AUTH_USER, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import {shareAction} from '../../config/utils/shareClass';
import ShopClass from '../../services/Shop.class';
const SuccessFormScreen = ({navigation, route}) => {
  useEffect(() => {
    fadeIn();
  }, []);
  const dispach = useDispatch();
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const fadeIn = () => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 1000,
    }).start();
  };

  const login = async (share) => {
    const {info} = route.params;
    try {
      dispach({
        layout: {
          layout: true,
        },
        type: RENDER_LAYOUT,
      });
      const shopClass = new ShopClass();
      const userData = await shopClass.getShopByNumber(info.phoneNumber);
      if (share) {
        shareAction(
            'Te invito a conocer mi tienda '+userData.shopName,
            `mitienda/${userData.idShop}`,
        );
      };
      navigation.navigate('HomeStore');
    } catch (e) {
      dispach({
        layout: {
          layout: false,
        },
        type: RENDER_LAYOUT,
      });
    }
  };
  return (
    <View
      style={[
        authStyles.interiorContainer,
        {backgroundColor: 'transparent'},
      ]}
    >
      <Background/>
      <View
        style={authStyles.responsiveContainer}
      >
        <Animated.View
          style={{opacity: fadeAnim}}
        >
          <Text
            style={[
              authStyles.cardTitle,
              {color: StylesVariables.SanLuis},
            ]}
          >
            ¡Felicidades, tu tienda ha sido creada!
          </Text>
          <Text
            style={[
              authStyles.textStyle,
              {color: StylesVariables.Bajio80p},
            ]}
          >
            {
              'Empieza a compartirla con tus clientes'
            }
          </Text>
          <Button
            uppercase={false}
            onPress={() => {
              login(true);
            }}
            id={'share_my_new_store'}
            title='Compartir mi tienda'
            fullWidth
            mode='buttonsGColors'
          />
          <View
            style={{padding: 10}}
          />
          <Button
            uppercase={false}
            onPress={() => {
              login(false);
            }}
            title='Ir a mi tienda'
            fullWidth
            mode={'buttonsGOutlineColors'}
          />
        </Animated.View>
      </View>
    </View>
  );
};

SuccessFormScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};

export default SuccessFormScreen;
