import {
  Animated,
  Image,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';
import PropTypes from 'prop-types';
import React, {useEffect, useRef} from 'react';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const MenuItem = ({
  title,
  subTitle,
  icon,
  active,
  loading,
  route,
  onPress,
}) => {
  useEffect(() => {
    fadeIn();
  }, []);

  const fadeAnim = useRef(new Animated.Value(0)).current;
  const fadeIn = () => Animated.loop(
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 1000,
      }),
      {
        iterations: 10,
      },
  ).start();
  if (loading) {
    return (
      <View
        style={styles.itemContainer}
      >
        <View
          style={styles.container}
        >
          <Animated.View
            style={{opacity: fadeAnim}}
          >
            <View
              style={styles.titleLoading}
            />
            <View
              style={styles.descriptionLoading}
            />
          </Animated.View>
        </View>
      </View>
    );
  };
  return (
    <View
      style={styles.itemContainer}
    >
      <TouchableOpacity
        style={{width: '100%'}}
        disabled={!active}
        onPress={onPress}
      >
        <View
          style={
            active ?
              styles.activeContainer :
              styles.container
          }
        >
          <View
            style={styles.background}
          >
            <Image
              source={icon}
              style={[
                styles.iconImage,
                {
                  tintColor: active ?
                    StylesVariables.Kometa :
                    StylesVariables.Asteroide40p,
                },
              ]}
            />
          </View>
          <View
            style={styles.titlesContainer}
          >
            <Text
              style={[
                styles.title,
                {
                  color: active ?
                    StylesVariables.Asteroide :
                    StylesVariables.Asteroide80p,

                },
              ]}
            >
              {title}
            </Text>
            <Text
              style={[
                styles.subTitle,
                {
                  color: active ?
                    StylesVariables.Asteroide :
                    StylesVariables.Asteroide80p,

                },
              ]}
            >
              {subTitle}
            </Text>
          </View>
        </View>
      </TouchableOpacity>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: 120 * StylesVariables.responsiveHeightMulti,
    borderRadius: 16 * StylesVariables.responsiveHeightMulti,
    backgroundColor: '#f3f3f5',
    justifyContent: 'center',
    overflow: 'hidden',
    padding: 15 * StylesVariables.responsiveHeightMulti,
    marginVertical: 8 * StylesVariables.responsiveHeightMulti,
  },
  activeContainer: {
    width: '100%',
    height: 120 * StylesVariables.responsiveHeightMulti,
    borderRadius: 16 * StylesVariables.responsiveHeightMulti,
    backgroundColor: 'white',
    justifyContent: 'center',
    overflow: 'hidden',
    borderWidth: 1,
    borderColor: StylesVariables.Kometa,
    marginVertical: 8 * StylesVariables.responsiveHeightMulti,
  },
  background: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    alignItems: 'flex-end',
  },
  iconImage: {
    width: 160,
    height: 160,
  },
  titleLoading: {
    width: 97 * StylesVariables.responsiveHeightMulti,
    height: 20 * StylesVariables.responsiveHeightMulti,
    backgroundColor: StylesVariables.Asteroide40p,
    borderRadius: 4 * StylesVariables.responsiveHeightMulti,
    marginVertical: 4 * StylesVariables.responsiveHeightMulti,
  },
  descriptionLoading: {
    width: 132 * StylesVariables.responsiveHeightMulti,
    height: 16 * StylesVariables.responsiveHeightMulti,
    backgroundColor: StylesVariables.Asteroide40p,
    borderRadius: 4 * StylesVariables.responsiveHeightMulti,
    marginVertical: 4 * StylesVariables.responsiveHeightMulti,
  },
  titlesContainer: {
    padding: 15 * StylesVariables.responsiveHeightMulti,
  },
  title: {
    fontFamily: 'NunitoBold',
    fontSize: 28 * StylesVariables.responsiveHeightMulti,
  },
  subTitle: {
    fontFamily: 'Regular',
    fontSize: 16 * StylesVariables.responsiveHeightMulti,
  },
  itemContainer: {
    marginHorizontal: 16 * StylesVariables.responsiveHeightMulti,
  },
});

MenuItem.propTypes = {
  title: PropTypes.string,
  subTitle: PropTypes.string,
  icon: PropTypes.any,
  active: PropTypes.bool,
  loading: PropTypes.bool,
  route: PropTypes.string,
  onPress: PropTypes.func,
};

export {MenuItem};
