import {Button, TextInput} from './index';
import PropTypes from 'prop-types';
import React, {useState} from 'react';
import {Modal, StyleSheet, View} from 'react-native';
import {Title} from 'react-native-paper';
import GestureRecognizer from 'react-native-swipe-gestures';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const ShopModal = ({
  isOpen,
  phoneNumber,
  name,
  changeForm,
  sendShopCart,
  closeModal,
}) => {
  const [errorNumber, setErrorNumber] = useState(' ');
  const [errorMessage, setErrorMessage] = useState(' ');
  const validateNumber = (text) => {
    let newText = '';
    const numbers = '0123456789';
    for (let i = 0; i < text.length; i++) {
      if (numbers.indexOf(text[i]) > -1 ) {
        newText = newText + text[i];
      } else {
        setErrorNumber('Este campo solo acepta numeros');
      }
    }
    if (newText !== phoneNumber) {
      setErrorNumber(' ');
    };
    changeForm('phone', newText);
  };

  const validate = () => {
    if (phoneNumber == '' || name == '') {
      setErrorMessage('Porfavor llene todos los campos');
      setErrorNumber('Porfavor llene todos los campos');
      return;
    };
    if (phoneNumber.length < 10) {
      setErrorNumber('Porfavor Introduzca un numero valido');
      return;
    };
    sendShopCart();
  };
  return (
    <Modal
      visible={isOpen}
      transparent={true}
      swipeDirection="down"
      onSwipeComplete={() => closeModal()}
    >
      <View
        style={styles.blackScreen}
      >
      </View>
      <GestureRecognizer
        style={styles.bottomButtonContainer}
        onSwipeDown={ () => closeModal() }
      >
        <View
          style={styles.formContainer}
        >
          <Title
            style={styles.title}
          >
            Rellena los campos solicitados
          </Title>
          <TextInput
            value={name}
            label='Nombre Completo'
            placeholder='Ej: Mariana Torres'
            type={errorMessage == ' ' ? 'primary' : 'danger'}
            helper={errorMessage}
            onChangeText={(value) => {
              if (
                !/^[a-zA-Z\s]*$/.test(value)
              ) {
                setErrorMessage(
                    'Este campo no acepta numeros ni caracteres especiales',
                );
                return;
              };
              setErrorMessage(' ');
              changeForm('name', value);
            }}
            maxLength={30}
          />
          <View
            style={{padding: 5}}
          />
          <TextInput
            value={phoneNumber}
            label='Número de WhatsApp'
            type={errorNumber == ' ' ? 'primary' : 'danger'}
            helper={errorNumber}
            placeholder='Ej: 2284934211'
            onChangeText={(value) => validateNumber(value)}
            maxLength={10}
          />
          <View
            style={{padding: 5}}
          />
          <Button
            title={'Realizar pedido por WhatsApp'}
            fullWidth={true}
            mode={'buttonsGColors'}
            onPress={validate}
          />
        </View>
      </GestureRecognizer>
    </Modal>
  );
};

ShopModal.propTypes = {
  isOpen: PropTypes.bool,
  phoneNumber: PropTypes.string,
  name: PropTypes.string,
  changeForm: PropTypes.func,
  sendShopCart: PropTypes.func,
  closeModal: PropTypes.func,
};
const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    justifyContent: 'flex-end',
  },
  blackScreen: {
    width: '100%',
    height: '100%',
    backgroundColor: 'black',
    opacity: 0.5,
    position: 'absolute',
  },
  bottomButtonContainer: {
    justifyContent: 'flex-end',
    flex: 1,
    width: '100%',
  },
  title: {
    fontFamily: 'NunitoBold',
  },
  formContainer: {
    borderTopStartRadius: 30,
    borderTopEndRadius: 30,
    backgroundColor: StylesVariables.Blanco,
    alignItems: 'center',
    padding:
      StylesVariables.spacing * 2 * StylesVariables.responsiveHeightMulti,
  },
});

export {ShopModal};
