import {Background} from '../../components/UI';
import {FlatList, Image, ScrollView, View} from 'react-native';
import {shareAction} from '../../config/utils/shareClass';
import {SquareButton, Navbar} from '../../components/UI';
import {Text, Title, useTheme} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';

// Styles
import {productDetailStyles} from './Product.styles';


// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Components
import ProductItem from '../../components/ProductItem/Details';

// Clases
import ShopClass from '../../services/Shop.class';

const ProductDetails = ({navigation, route}) => {
  const dispach = useDispatch();
  const {colors} = useTheme();
  const [product, setProduct] = useState({
    id: '',
    productName: '',
    productDescription: '',
    price: '',
    images: [],
  });
  useEffect(() => {
    const getProduct = navigation.addListener('focus', () => {
      getProductData();
    });

    return getProduct;
  }, []);
  const buttons = [
    {
      title: 'Compartir',
      source: require('../../../assets/icons/share.png'),
      action: async (id) => shareAction(
          `Hola! te invito a conocer ${product.productName} entrando a`,
          `product/${product.idProduct}`,
      ),
      id: 'share_my_product',
    },
    {
      title: 'Editar',
      source: require('../../../assets/icons/edit.png'),
      action: () => navigation.navigate('NewProduct', {
        productId: product.id,
        id: product.shop?.id,
        idProduct: product.idProduct,
      }),
      id: 'edit_product',
    },
  ];


  const getProductData = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      const getProduct = new ShopClass();
      const prod = await getProduct.getProductByShareId(
          route.params.id,
      );
      setProduct(prod);
    } catch (e) {
      console.log(e);
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };


  return (
    <View
      style={[
        productDetailStyles.container,
        {backgroundColor: 'transparent'},
      ]}
    >
      <Background/>
      <Navbar
        navigation={navigation}
      />
      <ScrollView
        style={productDetailStyles.scrollContainer}
        contentContainerStyle={productDetailStyles.contentContainer}
      >
        <View
          style={productDetailStyles.responsiveContainer}
        >
          <FlatList
            style={{width: '100%'}}
            horizontal
            data={product.images}
            renderItem={({item}) => {
              return (
                <ProductItem
                  articleImage={
                    item
                  }
                  carroussel={false}
                />
              );
            }}
            ListEmptyComponent={() => {
              return (
                <ProductItem/>
              );
            }}
          >
          </FlatList>
          <View
            style={productDetailStyles.row}
          >
            <Title
              style={productDetailStyles.title}
            >
              {product.productName}
            </Title>
          </View>
          <View
            style={{
              width: '100%',
              flexDirection: 'row',
            }}
          >
            <View
              style={[productDetailStyles.column, {alignItems: 'flex-start'}]}
            >
              <Text
                style={productDetailStyles.textPrice}
              >
                Precio
              </Text>
            </View>
            <View
              style={[productDetailStyles.column, {alignItems: 'flex-end'}]}
            >
              <Text
                style={productDetailStyles.textPrice}
              >
                ${product.price}
              </Text>
            </View>
          </View>
          <View
            style={productDetailStyles.row}
          >
            <View
              style={{flex: 1}}
            >
              <Text
                style={productDetailStyles.textNormal}
              >
                {product.productDescription}
              </Text>
            </View>
          </View>
          <View
            style={productDetailStyles.row}
          >
            {
              buttons.map((item, index) => {
                return (
                  <SquareButton
                    key={index}
                    category={'button'}
                    action={'click'}
                    id={item.id}
                    Icon={() => {
                      return (
                        <Image
                          source={item.source}
                          style={{width: 25, height: 25, tintColor: '#00D6AC'}}
                        />
                      );
                    }}
                    colorButton="white"
                    title={item.title}
                    onPress={() => item.action(product.id)}
                  />
                );
              })
            }
          </View>
        </View>
      </ScrollView>
    </View>
  );
};

ProductDetails.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};
export default ProductDetails;
