import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, TouchableOpacity, View} from 'react-native';

// Styles
import StylesVariables from './../config/Styles/app.style';

const CheckBoxCircle = ({value, onCheck}) => {
  return (
    <TouchableOpacity
      onPress={onCheck}
    >
      <View
        style={[
          styles.checkCircle,
          {
            backgroundColor: value ? '#414bb2' : 'white',
          },
        ]}
      >
        <View
          style={styles.checkInterior}
        />
      </View>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  checkCircle: {
    width: 21 * StylesVariables.responsiveHeightMulti,
    height: 21 * StylesVariables.responsiveHeightMulti,
    borderRadius: 25 * StylesVariables.responsiveHeightMulti,
    alignItems: 'center',
    justifyContent: 'center',
    borderWidth: 1,
    borderColor: '#d1d1d1',
  },
  checkInterior: {
    width: 9 * StylesVariables.responsiveHeightMulti,
    height: 9 * StylesVariables.responsiveHeightMulti,
    backgroundColor: 'white',
    borderRadius: 9 * StylesVariables.responsiveHeightMulti,
  },
});

CheckBoxCircle.propTypes = {
  value: PropTypes.bool,
  onCheck: PropTypes.func,
};
export default CheckBoxCircle;
