import {
  FlatList,
  Image,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  View,
} from 'react-native';
import {shareAction} from '../../config/utils/shareClass';
import {Background, ProductsBottomTab, SquareButton} from '../../components/UI';
import {Text, Title, useTheme} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';

// Styles
import {productDetailStyles} from './Product.styles';
import StylesVariables from './../../config/Styles/app.style';
// icons
import {AntDesign} from '@expo/vector-icons';

// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Components
import ProductItem from '../../components/ProductItem/Details';

// Clases
import ShopClass from '../../services/Shop.class';

// Icons
import {Feather} from '@expo/vector-icons';

const ProductDetails = ({navigation, route}) => {
  const dispach = useDispatch();
  const {colors} = useTheme();
  const [product, setProduct] = useState({
    id: '',
    productName: '',
    productDescription: '',
    price: '',
    images: [],
  });
  const [items, setItems] = useState(0);
  useEffect(() => {
    const getProduct = navigation.addListener('focus', () => {
      getProductData();
    });

    return getProduct;
  }, []);
  const buttons = [
    {
      title: 'Compartir',
      source: require('../../../assets/icons/share.png'),
      action: async (id) => shareAction(
          `Hola te invito a conocer ${product.productName} entrando a`,
          `product/${product.idProduct}`,
      ),
      id: 'share_product',
    },
  ];


  const getProductData = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      const {productId} = route.params;
      const getProduct = new ShopClass();
      const prod = await getProduct.getProductByShareId(
          productId,
      );
      setProduct(prod);
    } catch (e) {
      console.log(e);
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const shopProduct = () => {
    const store =
      route.params.store ?
      route.params.store :
      product.shop;

    navigation.navigate('CarInfo', {
      store,
      car: [
        {
          ...product,
          unit: items,
        },
      ],
    });
  };

  const addAction = (action) => {
    if (action == 'dimis') {
      if (items == 0) {
        return;
      };
      setItems(items - 1);
      return;
    };
    setItems(items + 1);
  };
  return (
    <View
      style={[
        productDetailStyles.container,
        {backgroundColor: colors.background},
      ]}
    >
      <Background/>
      <View
        style={productDetailStyles.responsiveContainer}
      >
        <View
          style={[
            styles.container,
          ]}
        >
          <TouchableOpacity
            style={{
              flexDirection: 'row',
              alignItems: 'center',
            }}
            onPress={() => navigation.navigate('Store', {
              idShop: product.shop.idShop,
            })}
          >
            <Feather
              name="arrow-left"
              size={20 * StylesVariables.responsiveHeightMulti}
              color={StylesVariables.KometaDark}
            />
            <Text
              style={styles.text}
            >
              {'  Regresar'}
            </Text>
          </TouchableOpacity>
        </View>
      </View>
      <ScrollView
        style={productDetailStyles.scrollContainer}
        contentContainerStyle={productDetailStyles.contentContainer}
      >
        <View
          style={productDetailStyles.responsiveContainer}
        >
          <FlatList
            style={{width: '100%'}}
            horizontal
            data={product.images}
            renderItem={({item}) => {
              return (
                <ProductItem
                  articleImage={
                    item
                  }
                  carroussel={false}
                />
              );
            }}
            ListEmptyComponent={() => {
              return (
                <ProductItem/>
              );
            }}
          >
          </FlatList>
          <View
            style={productDetailStyles.row}
          >
            <Title
              style={productDetailStyles.title}
            >
              {product.productName}
            </Title>
          </View>
          <View
            style={{
              width: '100%',
              flexDirection: 'row',
            }}
          >
            <View
              style={[productDetailStyles.column, {alignItems: 'flex-start'}]}
            >
              <Text
                style={productDetailStyles.textPrice}
              >
                Precio
              </Text>
            </View>
            <View
              style={[productDetailStyles.column, {alignItems: 'flex-end'}]}
            >
              <Text
                style={productDetailStyles.textPrice}
              >
                ${product.price}
              </Text>
            </View>
          </View>
          <View
            style={productDetailStyles.row}
          >
            <View
              style={{flex: 1}}
            >
              <Text
                style={productDetailStyles.textNormal}
              >
                {product.productDescription}
              </Text>
            </View>
          </View>
          <View
            style={productDetailStyles.gestorItemsContainer}
          >
            <TouchableOpacity
              style={{
                marginHorizontal: 8,
              }}
              onPress={() => addAction('dimis')}
            >
              <View
                style={productDetailStyles.buttonsRadius}
              >
                <AntDesign
                  name="minus"
                  size={20 * StylesVariables.responsiveHeightMulti}
                  color={StylesVariables.Kometa}
                />
              </View>
            </TouchableOpacity>
            <Text
              style={productDetailStyles.radiusText}
            >
              {items}
            </Text>
            <TouchableOpacity
              style={{
                marginHorizontal: 8,
              }}
              onPress={() => addAction('add')}
            >
              <View
                style={productDetailStyles.buttonsRadius}
              >
                <AntDesign
                  name="plus"
                  size={20 * StylesVariables.responsiveHeightMulti}
                  color={StylesVariables.Kometa}
                />
              </View>
            </TouchableOpacity>
          </View>
          <View
            style={productDetailStyles.row}
          >
            {
              buttons.map((btn, index) => {
                return (
                  <SquareButton
                    key={index}
                    id={btn.id}
                    Icon={() => {
                      return (
                        <Image
                          source={btn.source}
                          style={{width: 30, height: 30, tintColor: '#00D6AC'}}
                        />
                      );
                    }}
                    colorButton="white"
                    category={'button'}
                    action={'click'}
                    title={btn.title}
                    onPress={() => btn.action(product.idProduct)}
                  />
                );
              })
            }
          </View>
        </View>
      </ScrollView>
      <View
        style={productDetailStyles.responsiveContainer}
      >
        {
          items > 0 &&
            <ProductsBottomTab
              products={() => items}
              total={() => `Total $${product.price * items}`}
              onBuy={shopProduct}
            />
        }
      </View>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    width: '100%',
    padding: StylesVariables.spacing * StylesVariables.responsiveHeightMulti,
    flexDirection: 'row',
    backgroundColor: StylesVariables.Blanco,
  },
  text: {
    ...StylesVariables.appText,
    color: StylesVariables.KometaDark,
    fontSize: 13 * StylesVariables.responsiveHeightMulti,
    fontWeight: 'bold',
  },
});

ProductDetails.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};
export default ProductDetails;
