import {Card, Title} from 'react-native-paper';
import {Countdown} from 'react-native-reverse';
import PropTypes from 'prop-types';
import React, {useState} from 'react';
import {Image, Text, Platform, TouchableOpacity, View} from 'react-native';
import {
  CodeField,
  Cursor,
  useBlurOnFulfill,
  useClearByFocusCell,
} from 'react-native-confirmation-code-field';
// Clases
import ShopClass from '../../services/Shop.class';

// Redux
import {RENDER_LAYOUT, RENDER_MESSAGE, AUTH_USER} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Styles
import {authStyles} from './auth.styles';
import StylesVariables from '../../config/Styles/app.style';

// Componentes
import Logo from '../../components/Logo';
import {Background} from '../../components/UI';
const CELL_COUNT = 4;
const VerificationCodeScreen = ({navigation, route}) => {
  const [value, setValue] = useState('');
  const [enable, setEnable] = useState(false);
  const [countId, setCountId] = useState(false);
  const [errorCell, setErrorCell] = useState('');
  const ref = useBlurOnFulfill({value, cellCount: CELL_COUNT});

  const [props, getCellOnLayoutHandler] = useClearByFocusCell({
    value,
    setValue,
  });
  const dispach = useDispatch();

  const verificationCode = async (v) => {
    setValue(v);
    if (errorCell !== '') {
      setErrorCell('');
    }
    if (v.length < 4) {
      return;
    };
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });

    try {
      const verificate = new ShopClass();
      await verificate.getConfirmation(v, '52'+route.params.number);
      const verifyIfExist =
        await verificate.getShopByNumber(route.params.number);

      if (verifyIfExist) {
        if (Platform.OS === 'web') {
          localStorage.setItem('kometa-token', route.params.number);
        } else {
          await AsyncStorage.setItem('kometa-token', route.params.number);
        };
        dispach({
          userInfo: {
            number: route.params.number,
            userInfo: verifyIfExist,
          },
          type: AUTH_USER,
        });
      } else {
        await verificate.newShop({
          shopName: '',
          phoneNumber: route.params.number,
          address: '',
          latitude: 20.387298,
          longitude: -99.985317,
          shopDescription: '',
          category: '',
          image: '',
          storeDelivery: true,
          homeDelivery: 0,
        });
        const userInfo =
          await verificate.getShopByNumber(route.params.number);
        navigation.navigate('Welcome', {
          info: userInfo,
        });
      };
    } catch (e) {
      console.log(e);
      setValue('');
      setErrorCell('El código es invalido');
    };
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const sendMessage = async () => {
    if (!enable) {
      return;
    };
    try {
      dispach({
        layout: {
          layout: true,
        },
        type: RENDER_LAYOUT,
      });
      const request = new ShopClass();
      await request.sendMessage('+52'+route.params.number, route.params.name);
    } catch (e) {
      console.log(e);
    }
    setEnable(false);
    setCountId(!countId);
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  return (
    <View
      style={[
        authStyles.interiorContainer,
        {
          backgroundColor: 'transparent',
          alignItems: 'center',
        },
      ]}
    >
      <Background/>
      <Card
        style={authStyles.codeContainer}
      >
        <Logo/>
        <View
          style={authStyles.columnRow}
        >
          <Title
            style={authStyles.titleCards}
          >
            Ingresa el código
          </Title>
          <Title
            style={authStyles.titleCards}
          >
            enviado por WhatsApp
          </Title>
          <View
            style={{flexDirection: 'row', alignItems: 'center'}}
          >
            <Image
              source={require('../../../assets/icons/whatsapp.png')}
              style={{width: 25, height: 25}}
            />
            <Text
              style={[
                authStyles.anotation,
              ]}
            >
              {' a '}
            </Text>
            <Text
              style={authStyles.anotation}
            >
              {`+52 ${route.params.number}  `}
            </Text>
            <TouchableOpacity
              onPress={() => navigation.navigate('WhatsappRegister')}
            >
              <Text
                style={authStyles.noCodeText}
              >
                Editar
              </Text>
            </TouchableOpacity>
          </View>
        </View>
        <CodeField
          ref={ref}
          value={value}
          {...props}
          onChangeText={(v) => verificationCode(v)}
          cellCount={4}
          rootStyle={authStyles.codeFieldRoot}
          keyboardType="number-pad"
          textContentType="oneTimeCode"
          renderCell={({index, symbol, isFocused}) => {
            return (
              <Text
                key={index}
                style={[
                  errorCell == '' ?
                  authStyles.cell :
                  authStyles.errorCell,
                  isFocused && authStyles.focusCell,
                ]}
                onLayout={() => getCellOnLayoutHandler(index)}
              >
                {symbol}
              </Text>
            );
          }
          }
        />
        <View
          style={{flexDirection: 'row'}}
        >
          {
            enable &&
            <TouchableOpacity
              onPress={sendMessage}
              style={{flexDirection: 'row', alignItems: 'center'}}
            >
              <Text
                style={authStyles.codeCount}
              >
                Reenviar código
              </Text>
            </TouchableOpacity>
          }
          {
            !enable &&
            <TouchableOpacity>
              <View
                style={authStyles.codeCountContainer}
              >
                <Text
                  style={authStyles.codeCount}
                >
                  {'Reenviar código en '}
                </Text>
                <Countdown
                  until={59}
                  onFinish={() => {
                    setEnable(true);
                  }}
                  size={16}
                  timeToShow={['S']}
                  digitTxtStyle={
                    {
                      fontSize: 14 * StylesVariables.responsiveHeightMulti,
                      color: '#9FABA9',
                      fontWeight: 'bold',
                    }
                  }
                />
              </View>
            </TouchableOpacity>
          }
        </View>
      </Card>
    </View>
  );
};

VerificationCodeScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.object,
};

export default VerificationCodeScreen;
