import api from './../config/utils/callServices';
import {ApiDict} from '../config/utils/ApiDict';
import AsyncStorage from '@react-native-async-storage/async-storage';
import {Platform} from 'react-native';
import Constants from 'expo-constants';
import TakeImageClass from './../config/utils/ImagePreparison';
const envMode = Constants.manifest.extra.envMode;
/**
 * ShopClass controla todo
 * lo que tiene y esta
 * relacionado al propietario
 * de una tienda
 */
class ShopClass {
  /**
   * MessageTramit obtiene un
   * token de de sesion unico y lo
   * guarda en local storage para comprobar
   * si el usuario ha solicitado otp multiples veces
   * @return {promise}
  */
  getMessageTramit() {
    return new Promise(async (resolve, reject) => {
      let token;
      if (Platform.OS === 'web') {
        token = localStorage.getItem('tramit-token');
      } else {
        token = await AsyncStorage.getItem('tramit-token');
      };
      if (token) {
        resolve(token);
        return;
      };
      try {
        token = await api.get(ApiDict.message.getMessageTramit.url);
        if (Platform.OS === 'web') {
          localStorage.setItem('tramit-token', token);
        } else {
          await AsyncStorage.setItem('tramit-token', token);
        };
        resolve(token);
      } catch (e) {
        console.log(e);
        reject(e);
      };
    });
  };

  /**
  * @parametros {number} telefono
  * Represents a parametros.
  * Tramite del nuevo OTP
  * @return {promise}
  */
  messageTramit() {
    return new Promise(async (resolve, reject) => {
      try {
        const token = await this.getMessageTramit();
        const validate =
          await api.get(ApiDict.message.messageTramit.url+`/${token}`);
        if (validate) {
          resolve();
        } else {
          reject(
              new Error('Limite excedido'),
          );
        };
      } catch (e) {
        console.log(e);
        reject(e);
      }
    });
  };
  /**
  * @parametros {number} telefono
  * Represents a parametros.
  * Peticion get
  * @param {number} number representa el
  * telefono de la tienda
  */
  async sendMessage(number) {
    return new Promise(async (resolve, reject) => {
      if (envMode === 'DEV') {
        resolve('4444');
        return;
      };
      try {
        const token = await this.getMessageTramit();
        const request = await api.post(ApiDict.message.sendMessage.url, {
          phoneNumber: number,
          processId: token,
        });
        resolve(request);
      } catch (e) {
        reject(e);
      };
    });
  };
  /**
  * @parametros {number} code
  * @parametros {number} telefono
  * Represents a parametros.
  * Peticion get
  * @param {code} code representa el
  * codigo de verificacion
  * @param {number} number representa el
  * numero telefonico
  */
  async getConfirmation(code, number) {
    return new Promise(async (resolve, reject) => {
      if (envMode == 'DEV') {
        resolve('4444');
        return;
      };
      try {
        const request =
          await api.get(`${ApiDict.message.getMessage.url}/${code}/${number}`);
        if (Platform.OS === 'web') {
          localStorage.removeItem('tramit-token');
        };
        resolve(request);
      } catch (e) {
        reject(e);
      };
    });
  };
  /**
  * @parametros {object} form
  * represents a parametros.
  * peticion get
  * @param {object} form representa el
  * formulario que se enviara al backend
  * el cual incluye shopname, phonenumber,
  * address, latitude, longitude,
  * shopdescription, category, image(base64),
  * storedelivery(recogida en tienda),
  * homedelivery(radio de entrega a domicilio)
  */
  async newShop(form) {
    return new Promise(async (resolve, reject) => {
      try {
        const request = await api.post(
            ApiDict.shop.createShop.url,
            form,
        );
        resolve(request);
      } catch (e) {
        reject(e);
      };
    });
  };
  /**
  * @parametros {object} form
  * @parametros {string} id de la tienda
  * represents a parametros.
  * peticion get
  * @param {object} form representa el
  * @param {string} shopId es el id de la tienda
  * formulario que se enviara al backend
  * el cual incluye shopname, phonenumber,
  * address, latitude, longitude,
  * shopdescription, category, image(base64),
  * storedelivery(recogida en tienda),
  * homedelivery(radio de entrega a domicilio)
  */
  async alterShop(form, shopId) {
    return new Promise(async (resolve, reject) => {
      try {
        const request = await api.put(
            ApiDict.shop.alterShop.url+'/'+shopId,
            form,
        );
        resolve(request);
      } catch (e) {
        reject(e);
      };
    });
  };
  /**
  * Pide todas las categorias existentes
  */
  async getCategories() {
    return new Promise(async (resolve, reject) => {
      try {
        const request = await api.get(ApiDict.category.getCategories.url);
        resolve(request);
      } catch (e) {
        reject(e);
      }
    });
  };
  /**
  * @parametros {number} phoneNumber
  * represents a parametros.
  * peticion get
  * @param {number} phoneNumber representa el
  * numero de telefono correspondiente a una tienda
  * y este sera buscado en la base de datos
  * @return {promise}
  */
  getShopByNumber(phoneNumber) {
    return new Promise(async (resolve, reject) => {
      try {
        const store = await api.get(
            ApiDict.shop.getShopByNumber.url+'/'+phoneNumber,
        );
        resolve(store);
      } catch (e) {
        e = JSON.parse(e);
        if (e.status == 404) {
          resolve(false);
          return;
        };
        reject(e);
      }
    });
  }
  /**
  * @parametros {string} id del store
  * represents a parametros.
  * peticion get
  * @param {number} storeId representa el id del
  * store que sera buscado en la base de datos
  * @return {promise}
  */
  getShopById(storeId) {
    return new Promise(async (resolve, reject) => {
      try {
        const store = await api.get(
            ApiDict.shop.getShopById.url+'/'+storeId,
        );
        resolve(store);
      } catch (e) {
        e = JSON.parse(e);
        if (e.status == 404) {
          resolve(false);
          return;
        };
        reject(e);
      }
    });
  }
  /**
  * @parametros {form} formulario
  * represents a parametros.
  * esta funcion validara el formulario correspondiente
  * al producto
  * @param {object} form es el formulario
  * que se validara con el producto, de acuerdo a la regla
  * ningun campo debe estar vacio salvo el de la imagen
  * que puede ser un array vacio
  * @return {promise}
  */
  validateProductForm(form) {
    return new Promise( async (resolve, reject) => {
      const formKeys = Object.keys(form);
      const errors = [];
      for (const key of formKeys) {
        if (key !== 'image') {
          if (form[key] == '') {
            errors.push(key);
          };
        };
      };
      if (errors.length > 0) {
        reject(errors);
      };
      if (form.price[0] == '$') {
        form.price = form.price.substring(1);
      };
      const formu = {
        productName: form.name,
        price: form.price,
        productDescription: form.description,
        images: form.image,
        idShop: form.id,
        idProduct: form.idProduct,
      };
      formu.images = await TakeImageClass.formatImageArr(formu.images);
      resolve(formu);
    });
  };
  /**
  * @parametros {form} formulario
  * represents a parametros.
  * peticion get
  * numero de telefono correspondiente a una tienda
  * @param {object} form es el formulario que se enviara para
  * crear la tienda e incluye productName,
  * price, image (array base64), idShop(id de la tienda)
  * y este sera buscado en la base de datos
  * @return {promise}
  */
  addProduct(form) {
    return new Promise(async (resolve, reject) => {
      try {
        const validate = await this.validateProductForm(form);
        const add = await api.post(
            ApiDict.shop.addProduct.url,
            validate,
        );
        resolve(add);
      } catch (e) {
        reject(
            e,
        );
      }
    });
  };
  /**
  * @parametro {id} id del producto
  * @parametros {form} formulario
  * represents a parametros.
  * peticion get
  * numero de telefono correspondiente a una tienda
  * @param {object} form es el formulario que se enviara para
  * @param {string} id es el id del producto que se quiere editar
  * crear la tienda e incluye productName,
  * price, image (array base64), idShop(id de la tienda)
  * y este sera buscado en la base de datos
  * @return {promise}
  */
  updateProduct(form, id) {
    return new Promise(async (resolve, reject) => {
      try {
        const validate = await this.validateProductForm(form);
        const add = await api.put(
            ApiDict.shop.alterProduct.url+'/'+id,
            validate,
        );
        resolve(add);
      } catch (e) {
        reject(
            e,
        );
      }
    });
  };
  /**
  * @parametros {shopId} id de la tienda
  * represents a parametros.
  * peticion get
  * numero de telefono correspondiente a una tienda
  * @param {string} shopId id de la tienda
  * @return {promise}
  */
  getShopByShareId(shopId) {
    return new Promise(async (resolve, reject) => {
      try {
        const getShop = await api.get(
            'shop/'+shopId+'/'+ApiDict.shop.getShopByShareId.url,
        );
        resolve(getShop);
      } catch (e) {
        reject(e);
      }
    });
  };
  /**
  * @parametros {productId} id de la tienda
  * represents a parametros.
  * peticion get
  * @param {string} productId id del producto
  * @return {promise}
  */
  getProductByShareId(productId) {
    return new Promise(async (resolve, reject) => {
      try {
        const getProduct = await api.get(
            'product/'+productId+'/'+ApiDict.shop.getProductByShareId.url,
        );
        resolve(getProduct);
      } catch (e) {
        reject(e);
      }
    });
  };
  /**
  * @parametros {shopId} id de la tienda
  * represents a parametros.
  * peticion get
  * numero de telefono correspondiente a una tienda
  * @param {string} shopId id de la tienda
  * @return {promise}
  */
  getAllProductsById(shopId) {
    return new Promise(async (resolve, reject) => {
      try {
        const getProducts = await api.get(
            ApiDict.shop.getAllProductsById.url+'/'+shopId,
        );
        resolve(getProducts);
      } catch (e) {
        reject(e);
      }
    });
  };
  /**
  * @parametros {productId} id del Producto
  * represents a parametros.
  * peticion get
  * numero de telefono correspondiente a una tienda
  * @param {string} productId del Producto
  * @return {promise}
  */
  getOneProductById(productId) {
    return new Promise(async (resolve, reject) => {
      try {
        const productData = await api.get(
            ApiDict.shop.getOneProductById.url+'/'+productId,
        );
        resolve(productData);
      } catch (e) {
        reject(e);
      };
    });
  };
  /**
  * @parametros {productId} id del Producto
  * represents a parametros.
  * peticion get
  * numero de telefono correspondiente a una tienda
  * @param {string} productId del Producto
  * @return {promise}
  */
  deleteProductById(productId) {
    return new Promise(async (resolve, reject) => {
      try {
        await api.delete(
            ApiDict.shop.deleteProduct.url+'/'+productId,
        );
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  };

  // Este metodo no tiene que ver nada con los metodos de la tiendaa
  /**
  * @parametros {email} direccion de email
  * represents a parametros.
  * peticion post
  * @param {string} email
  * @return {promise}
  */
  getEmailAddress(email) {
    return new Promise(async (resolve, reject) => {
      try {
        await api.post('email', {
          email: email,
        });
        resolve();
      } catch (e) {
        reject(e);
      };
    });
  };
};

export default ShopClass;
