import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, View} from 'react-native';
import {Text} from 'react-native-paper';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const InfoComponent = ({Icon, info}) => {
  const icon = () => {
    if (!Icon) {
      return null;
    };
    return <Icon/>;
  };
  return (
    <View
      style={styles.container}
    >
      <View
        style={{flex: .2}}
      >
        {icon()}
      </View>
      <View
        style={{flex: .8}}
      >
        <Text
          style={styles.text}
        >
          {info}
        </Text>
      </View>
    </View>
  );
};

InfoComponent.propTypes = {
  Icon: PropTypes.any,
  info: PropTypes.string,
};

const styles = StyleSheet.create({
  container: {
    width: '100%',
    padding: 16 * StylesVariables.responsiveHeightMulti,
    backgroundColor: StylesVariables.Info40p,
    flexDirection: 'row',
    alignItems: 'center',
    marginVertical: 8 * StylesVariables.responsiveHeightMulti,
  },
  text: {
    fontFamily: 'SemiBold',
    color: StylesVariables.Info,
    fontSize: 12,
  },
});
export {InfoComponent};
