import {Button as PaperButton, useTheme, Platform} from 'react-native-paper';
import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, View} from 'react-native';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const Button = ({
  type = 'primary',
  onPress,
  title,
  disabled = false,
  mode = 'buttonsColors',
  fullWidth = false,
  buttonSty,
  textSty,
  out = false,
  Icon,
  id,
  category,
  action,
}) => {
  const {dark} = useTheme();
  const {
    buttonsColors,
    buttonsGColors,
    buttonsOutlineColors,
    buttonsGOutlineColors,
    buttonsDarkColors,
    buttonsGDarkColors,
    buttonsDarkOutlineColors,
    buttonsGDarkOutlineColors,
  } = StylesVariables;

  const palette = !dark ? {
    buttonsColors,
    buttonsGColors,
    buttonsOutlineColors,
    buttonsGOutlineColors,
  } : {
    buttonsColors: buttonsDarkColors,
    buttonsGColors: buttonsGDarkColors,
    buttonsOutlineColors: buttonsDarkOutlineColors,
    buttonsGOutlineColors: buttonsGDarkOutlineColors,
  };

  const colorButton = (label) => {
    let tempObj = palette[mode];
    tempObj = tempObj[type];
    if (label) return tempObj.label;
    return tempObj;
  };

  const buttonStyle = {
    backgroundColor: colorButton().backgroundColor,
    borderColor: colorButton().borderColor,
    borderWidth: colorButton().borderWidth,
  };

  const labelStyle = {
    color: colorButton(true).color,
    fontSize: colorButton(true).fontSize,
    fontFamily: colorButton(true).fontFamily,
  };

  const renderIcon = () => {
    if (!Icon) {
      return null;
    };
    return <Icon/>;
  };

  const pressButton = async () => {
    if (id) {
      await window.dataLayer.push({
        event: id,
        eventProps: {
          category: category,
          action: action,
          label: id,
          value: '',
        },
      });
    };
    onPress();
  };

  return (
    <button
      style={webStyle(fullWidth).container}
      onClick={pressButton}
      id={id}
      disabled={disabled}
    >
      <PaperButton
        uppercase={false}
        disabled={disabled}
        mode={!out ? 'contained' : 'outlined'}
        style={[
          !disabled ? buttonStyle : palette[mode].disabled,
          fullWidth ? {width: '100%'} : {},
          buttonSty ? buttonSty : {},
          {zIndex: -5},
        ]}
        labelStyle={[
          !disabled ? labelStyle : palette[mode].disabled,
          textSty ? textSty : {},
        ]}
      >
        {'  '+title+'  '}{renderIcon()}
      </PaperButton>
    </button>
  );
};

const webStyle = (fullWidth) => {
  return {
    container: {
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 10,
      backgroundColor: 'transparent',
      borderColor: 'transparent',
      cursor: 'pointer',
      width: fullWidth ? '100%' : 'auto',
    },
  };
};
const styles = StyleSheet.create({
  container: {
    alignItems: 'center',
    justifyContent: 'center',
  },
  textStyle: {
    fontSize: 1 * StylesVariables.responsiveHeightMulti,
  },
});
Button.propTypes = {
  type: PropTypes.string,
  onPress: PropTypes.func,
  title: PropTypes.string,
  disabled: PropTypes.bool,
  outlined: PropTypes.bool,
  big: PropTypes.bool,
  mode: PropTypes.string,
  fullWidth: PropTypes.bool,
  buttonSty: PropTypes.any,
  textSty: PropTypes.any,
  out: PropTypes.bool,
  Icon: PropTypes.func,
  id: PropTypes.string,
  category: PropTypes.string,
  action: PropTypes.string,
};
export {Button};
