import api from './api';
/**
 * CallServices centraliza las http requests
 */
class CallServices {
  /**
  * @parametros {string} parametros get
  * Represents a parametros.
  * Peticion get
  * @param {string} params representa los parametros de ruta.
  */
  async get(params) {
    return new Promise(async (resolve, reject) => {
      try {
        const request = await api.get(params);
        if (request.status === 200 &&
          request.data
        ) {
          resolve(request.data);
        } else {
          reject(JSON.stringify(error));
        }
      } catch (error) {
        reject(JSON.stringify(error));
      }
    });
  };

  /**
  * @parametros {string} parametros get
  * @parametros {object} formData
  * Represents a parametros.
  * Peticion post
  * @param {string} params representa los parametros de ruta.
  * @param {object} form representa el formulario en un json.
  */
  async post(params, form) {
    return new Promise(async (resolve, reject) => {
      const formSend = JSON.stringify(form);
      try {
        const request = await api.post(params, formSend);
        if (
          request.status === 200 ||
          request.status === 201
        ) {
          resolve(request.data);
        } else {
          reject(new Error('Err'));
        }
      } catch (error) {
        reject(JSON.stringify(error));
      }
    });
  };
  /**
  * @parametros {string} parametros get
  * @parametros {object} formData
  * Represents a parametros.
  * Peticion put
  * @param {string} params representa los parametros de ruta.
  * @param {object} form representa el formulario en un json.
  */
  async put(params, form) {
    return new Promise(async (resolve, reject) => {
      const formSend = JSON.stringify(form);
      try {
        const request = await api.put(params, formSend);
        if (
          request.status === 200 ||
          request.status === 204
        ) {
          resolve(request.data);
        } else {
          reject(new Error('Err'));
        }
      } catch (error) {
        reject(JSON.stringify(error));
      }
    });
  };
  /**
  * @parametros {string} parametros get
  * Represents a parametros.
  * Peticion detele
  * @param {string} params representa los parametros de ruta.
  */
  async delete(params) {
    return new Promise(async (resolve, reject) => {
      try {
        const request = await api.delete(params);
        if (
          request.status === 204 ||
          request.status === 201
        ) {
          resolve(request.data);
        } else {
          reject(new Error('Err'));
        }
      } catch (error) {
        reject(JSON.stringify(error));
      }
    });
  };
}

export default new CallServices();
