const generateButton = (
    labelSize = 12,
    labelColor = 'black',
    buttonBground = AppTheme.Kometa,
    borderWidth = 0,
    borderColor = 'transparent',
) => {
  return {
    backgroundColor: buttonBground,
    borderWidth: borderWidth,
    borderColor: borderColor,
    label: {
      fontFamily: 'SemiBold',
      fontSize: labelSize * responsiveHeightMulti,
      color: labelColor,
    },
  };
};

