import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, TouchableOpacity, View, Text} from 'react-native';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const SquareMinButton = ({
  colorButton = '#65679E',
  Icon = null,
  onPress,
  title= 'lorem',
  id,
  category,
  action,
}) => {
  if (!Icon) {
    return null;
  }

  const pressButton = async () => {
    if (id) {
      await window.dataLayer.push({
        event: id,
        eventProps: {
          category: category,
          action: action,
          label: id,
          value: '',
        },
      });
    };
    onPress();
  };
  return (
    <button
      id={id}
      style={webStyle.container}
      onClick={pressButton}
    >
      <View
        style={styles.extContainer}
      >
        <TouchableOpacity
        >
          <View
            style={[
              styles.container,
              {backgroundColor: colorButton},
            ]}
          >
            <Icon/>
          </View>
        </TouchableOpacity>
      </View>
    </button>
  );
};

SquareMinButton.propTypes = {
  colorButton: PropTypes.string,
  Icon: PropTypes.func,
  onPress: PropTypes.func,
  title: PropTypes.string,
  id: PropTypes.string,
  category: PropTypes.string,
  action: PropTypes.string,
};

const webStyle = {
  container: {
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 10,
    backgroundColor: 'transparent',
    borderColor: 'transparent',
    cursor: 'pointer',
    width: 30 * StylesVariables.responsiveHeightMulti,
    height: 30 * StylesVariables.responsiveHeightMulti,
    marginRight: 8,
  },
};
const styles = StyleSheet.create({
  container: {
    width: 30 * StylesVariables.responsiveHeightMulti,
    height: 30 * StylesVariables.responsiveHeightMulti,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 8 * StylesVariables.responsiveHeightMulti,
  },
  extContainer: {
    alignItems: 'center',
    marginRight: 8,
    zIndex: -10,
  },
  anotationTitle: {
    ...StylesVariables.appText,
    fontSize: 12 * StylesVariables.responsiveHeightMulti,
    color: StylesVariables.Asteroide80p,
    marginTop: 5,
  },
});

export {SquareMinButton};
