import Constants from 'expo-constants';
import * as Linking from 'expo-linking';

const url = Constants.manifest.extra.envUrl;
const phoneKometa = '5219992977085';
export const shareAction = (message, param) => {
  Linking.openURL(`https://api.whatsapp.com/send?text=${message} ${url+param}`);
};

export const buyAction = (message, param, phoneNumber) => {
  Linking.openURL(`https://wa.me/${phoneNumber}?text=${message} Tienda: ${url+param}`);
};

export const questionsKometa = () => {
  Linking.openURL(`https://wa.me/${phoneKometa}?text=${'Hola tengo una duda sobre Kometa.shop'}`);
};
