import {Background, Button} from '../../components/UI';
import {Animated, View} from 'react-native';
import {Text} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useRef} from 'react';

// Styles
import {authStyles} from './../auth/auth.styles';
import StylesVariables from './../../config/Styles/app.style';

const SuccessFormScreen = ({navigation, route}) => {
  useEffect(() => {
    setTimeout(() => {
      fadeIn();
    }, 2000);
    setTimeout(() =>{
      secondLeterAnim();
    }, 2000);
  }, []);
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const secondLeter = useRef(new Animated.Value(0)).current;
  const fadeIn = () => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 1000,
    }).start();
  };
  const secondLeterAnim = () => {
    Animated.timing(secondLeter, {
      toValue: 1,
      duration: 2000,
    }).start();
  };

  return (
    <View
      style={[
        authStyles.interiorContainer,
        {backgroundColor: 'transparent'},
      ]}
    >
      <Background/>
      <View
        style={authStyles.responsiveContainer}
      >
        <Animated.View
          style={{opacity: fadeAnim}}
        >
          <Text
            style={[
              authStyles.cardTitle,
              {color: StylesVariables.Irapuato},
            ]}
          >
            {'Hubo un error con tus datos ¡no te preocupes!'}
          </Text>
        </Animated.View>
        <Animated.View
          style={{opacity: secondLeter}}
        >
          <Text
            style={[
              authStyles.textStyle,
              {color: StylesVariables.Bajio80p},
            ]}
          >
            Revisa que tus
            datos estén correctos,
            ya estas a un paso de
            crear tu negocio con Kometa
          </Text>
          <View
            style={authStyles.buttonContainer}
          >
            <Button
              uppercase={false}
              onPress={() => {
                navigation.goBack();
              }}
              title='Regresar'
              fullWidth
              mode={'buttonsGColors'}
            />
          </View>
        </Animated.View>
      </View>
    </View>
  );
};

SuccessFormScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};

export default SuccessFormScreen;
