import PropTypes from 'prop-types';
import React from 'react';
import {ScrollView, Share, TouchableOpacity, View} from 'react-native';
import {Text, Title, useTheme} from 'react-native-paper';

// Styles
import {productDetailStyles} from './Product.styles';

// Icons
import {Ionicons} from '@expo/vector-icons';

// Components
import ProductItem from '../../components/ProductItem/Details';

const itemObj = {
  title: 'Aretes diseno de flor multicolor',
  taille: 80,
  articleImage:
   'https://cdn.cliqueinc.com/posts/197779/colors-that-look-good-with-red-2016-197779-1572478072040-main.700x0c.jpg',
  desc: '',
  min: 3,
  max: 6,
};
const SharedDetails = ({navigation}) => {
  const {colors} = useTheme();
  const onShare = async () => {
    try {
      await Share.share({
        message:
          `Mira! ${itemObj.title} en mi almacen meta`,
      });
    } catch (error) {
      alert(error.message);
    }
  };

  return (
    <View
      style={[
        productDetailStyles.container,
        {backgroundColor: colors.background},
      ]}
    >
      <ScrollView
        style={productDetailStyles.scrollContainer}
      >
        <ProductItem
          articleImage={itemObj.articleImage}
          carroussel={false}
        />
        <View
          style={productDetailStyles.row}
        >
          <Title
            style={productDetailStyles.title}
          >
            {itemObj.title}
          </Title>
        </View>
        <View
          style={productDetailStyles.row}
        >
          <TouchableOpacity
            onPress={onShare}
          >
            <View
              style={{flexDirection: 'row', alignItems: 'center'}}
            >
              <View
                style={productDetailStyles.buttonCircle}
              >
                <Ionicons name="arrow-redo" size={18} color="#652cb3" />
              </View>
              <Text
                style={productDetailStyles.textButtonShare}
              >
                Compartir con tu clienta
              </Text>
            </View>
          </TouchableOpacity>
        </View>
        <View
          style={productDetailStyles.row}
        >
          <Text
            style={productDetailStyles.textNormal}
          >
            Reloj Numeros Romanos y Calendario Extensible
            Piel Sintética Con Caratula Rosa proveedores
            directos de fabrica R0159
          </Text>
        </View>
        <View
          style={productDetailStyles.row}
        >
          <View
            style={[{alignItems: 'flex-start'}]}
          >
            <Text
              style={[
                productDetailStyles.textNormal,
                {color: '#9510ac', fontSize: 15},
              ]}
            >
              Precio de venta ${itemObj.taille}
            </Text>
          </View>
        </View>
      </ScrollView>
    </View>
  );
};

SharedDetails.propTypes = {
  navigation: PropTypes.any,
};
export default SharedDetails;
