import {
  ImageBackground,
  StyleSheet,
  TouchableOpacity,
  View,
} from 'react-native';
import PropTypes from 'prop-types';
import React from 'react';
import StylesVariables from '../../config/Styles/app.style';
import {Text, useTheme} from 'react-native-paper';

// Icons
import {AntDesign} from '@expo/vector-icons';

const AddImage = ({
  title,
  image,
  onAddImage,
  removeImage,
  disabled = false,
}) => {
  const {colors} = useTheme();
  if (image) {
    return (
      <View>
        <View
          style={styles.container}
        >
          <ImageBackground
            style={{width: '100%', height: '100%'}}
            source={{uri: image.uri ? image.uri : image}}
          />
        </View>
        <View
          style={{
            flexDirection: 'row',
            width: 100 * StylesVariables.responsiveHeightMulti,
            marginVertical: 5,
          }}
        >
          <View
            style={{
              flex: 1,
              alignItems: 'center',
            }}
          >
            <TouchableOpacity
              onPress={removeImage}
            >
              <AntDesign
                name="close"
                size={18 * StylesVariables.responsiveHeightMulti}
                color={'#F46263'}
              />
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  };

  return (
    <View>
      <TouchableOpacity
        onPress={onAddImage}
        disabled={disabled}
      >
        <View
          style={styles.container}
        >
          <AntDesign
            name="pluscircle"
            size={24 * StylesVariables.responsiveHeightMulti}
            color={StylesVariables.Kometa}
          />
        </View>
      </TouchableOpacity>
    </View>
  );
};

AddImage.propTypes = {
  title: PropTypes.any,
  image: PropTypes.any,
  onAddImage: PropTypes.func,
  removeImage: PropTypes.func,
  disabled: PropTypes.bool,
};

const styles = StyleSheet.create({
  container: {
    width: 100 * StylesVariables.responsiveHeightMulti,
    height: 100 * StylesVariables.responsiveHeightMulti,
    backgroundColor: '#EBECF2',
    borderRadius: 4 * StylesVariables.responsiveHeightMulti,
    overflow: 'hidden',
    alignItems: 'center',
    justifyContent: 'center',
    marginRight: 8,
  },
  title: {
    ...StylesVariables.appText,
    fontSize: 12 * StylesVariables.responsiveHeightMulti,
    marginVertical: 5,
  },
});

export {AddImage};
