import {
  Button,
  SquareButton,
  InfoComponent,
  Logo as StoreLogo,
  Background,
} from './../../components/UI';
import {Card, Text, Paragraph} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';
import {shareAction, questionsKometa} from '../../config/utils/shareClass';
import {
  ImageBackground,
  ScrollView,
  View,
  Image,
} from 'react-native';
import Product from '../../components/Product';

// Styles
import {homeStoreStyles as styles} from './home.styles';

// Icons
import {MaterialCommunityIcons} from '@expo/vector-icons';

// redux
import {RENDER_LAYOUT, RENDER_MESSAGE} from '../../redux/constants';
import {useSelector, useDispatch} from 'react-redux';
import {authAndDrive} from '../../redux/actions/userActions';

// Classes
import ShopClass from '../../services/Shop.class';

import Logo from '../../components/Logo';
import {defaultHeader} from '../../config/utils/objectForlogo';
const limitInfo =
  'Te informamos que el límite de productos que puedes ingresar son 10';

const HomeStoreScreen = ({navigation}) => {
  useEffect(() => {
    const getProducts = navigation.addListener('focus', () => {
      getAllStore();
    });

    return getProducts;
  }, []);

  const dispach = useDispatch();
  const [products, setProducts] = useState([]);
  const [banner, setBanner] = useState();

  const {user} = useSelector(
      (state) => ({
        user: authAndDrive(state),
      }),
  );
  const userData = user.userInfo.user.userInfo;
  const buttons = [
    {
      title: 'Compartir',
      source: require('../../../assets/icons/share.png'),
      action: () => {
        shareAction(
            'Hola! te invito a conocer '+userData.shopName+' entrando a',
            `mitienda/${userData.idShop}`,
        );
      },
      id: 'share_my_store',
    },
    {
      title: 'Editar',
      source: require('../../../assets/icons/edit.png'),
      action: () => {
        navigation.navigate('AlterMarket', {
          storeId: userData.id,
        });
      },
      id: null,
    },
    {
      title: 'Ayuda',
      source: require('../../../assets/icons/help.png'),
      action: () => questionsKometa(),
      id: 'home_help',
    },
  ];
  const getAllStore = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      const shop = new ShopClass();
      const store = await shop.getAllProductsById(userData.id);
      if (!userData.banner) {
        for (const header of defaultHeader) {
          if (header.types.includes(userData.category.description.trim())) {
            setBanner(header.src);
            break;
          };
        };
      } else {
        setBanner({uri: userData.banner});
      };
      setProducts(store);
    } catch (e) {
      dispach({
        message: {
          show: true,
          message: 'No se puede acceder a su solicitud de momento',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const addProduct = () => {
    if (products.length >= 10) {
      return;
    };
    navigation.navigate('NewProduct', {
      info: userData,
    });
  };
  return (
    <View
      style={styles.container}
    >
      <Background/>
      <ScrollView
        style={styles.scrollContainer}
        contentContainerStyle={{alignItems: 'center'}}
      >
        <View
          style={styles.responsiveContainer}
        >
          <View
            style={styles.banner}
          >
            {
              banner &&
                <ImageBackground
                  source={banner}
                  style={{
                    width: '100%',
                    height: '100%',
                    position: 'absolute',
                  }}
                />
            }
            <View
              style={styles.bannerInterior}
            >
              <View
                style={{flex: 1}}
              >
                <Logo
                  white={true}
                />
              </View>
              <View
                style={{flex: 1, alignItems: 'flex-end'}}
              >
                <Button
                  type={'white'}
                  title={'Mis recursos'}
                  onPress={() => navigation.navigate('MainMenu')}
                />
              </View>
            </View>
          </View>
        </View>
        <View
          style={styles.storeContainer}
        >
          <View
            style={styles.editStoreContainer}
          >
            <Text
              style={styles.helloStore}
            >
              {'¡Hola de nuevo!'}
            </Text>
          </View>
          <Card
            style={{borderRadius: 20}}
          >
            <Card.Content
              style={styles.profileContainer}
            >
              <View
                style={{flex: .3}}
              >
                {
                  !userData.image &&
                    <StoreLogo
                      category={userData.category.description}
                      name={userData.shopName}
                    />
                }
                {
                  userData.image &&
                    <View
                      style={styles.pictureContainer}
                    >
                      <ImageBackground
                        source={{uri: userData.image}}
                        style={{width: '100%', height: '100%'}}
                      />
                    </View>
                }
              </View>
              <View
                style={{flex: .7}}
              >
                <View
                  style={styles.titleContainer}
                >
                  <Text
                    style={styles.titleProfile}
                  >
                    {userData.shopName}
                  </Text>
                </View>
                <View
                  style={styles.titleContainer}
                >
                  <Paragraph
                    style={styles.locationText}
                  >
                    <MaterialCommunityIcons
                      name="map-marker-outline"
                      style={styles.locationIcon}
                    />
                    {' '+userData.address}
                  </Paragraph>
                  {
                    userData.homeDelivery !== 0 &&
                    <Paragraph
                      style={styles.locationText}
                    >
                      <MaterialCommunityIcons
                        name="home-outline"
                        style={styles.locationIcon}
                      />
                      {' Envios a domicilio'}
                    </Paragraph>
                  }
                  {
                    userData.storeDelivery !== 0 &&
                    <Paragraph
                      style={styles.locationText}
                    >
                      <MaterialCommunityIcons
                        name="storefront-outline"
                        style={styles.locationIcon}
                      />
                      {' Recoger en tienda'}
                    </Paragraph>
                  }
                </View>
              </View>
            </Card.Content>
            <Card.Content>
              <View
                style={styles.descriptionContainer}
              >
                <Text
                  style={styles.descriptionProfileText}
                >
                  DESCRIPCIÓN
                </Text>
                <Text
                  style={styles.descriptionOfProfile}
                >
                  {userData.shopDescription}
                </Text>
              </View>
            </Card.Content>
          </Card>
          <Text
            style={[
              styles.titleProfile,
              {marginBottom: 30, marginTop: 10},
            ]}
          >
            Acciones de mi tienda
          </Text>
          <View
            style={[
              styles.row,
              {flexDirection: 'row'},
            ]}
          >
            {
              buttons.map((item, index) => {
                return (
                  <SquareButton
                    key={index}
                    id={item.id}
                    category={'button'}
                    action={'click'}
                    Icon={() => {
                      return (
                        <Image
                          source={item.source}
                          style={{
                            width: 25,
                            height: 25,
                            tintColor: '#67F1D6',
                          }}
                        />
                      );
                    }}
                    colorButton="white"
                    title={item.title}
                    onPress={() => item.action()}
                  />
                );
              })
            }
          </View>

          <Text
            style={[
              styles.titleProfile,
              {marginVertical: 20},
            ]}
          >
            Productos
          </Text>
          <View
            style={styles.row}
          >
            {
              products.length < 10 &&
              <Button
                id={'add_product_button'}
                title={'Agregar productos'}
                mode={'buttonsGColors'}
                onPress={addProduct}
                category={'button'}
                action={'click'}
              />
            }
          </View>
          {
            products.length >= 10 &&
            <InfoComponent
              info={limitInfo}
              Icon={() => {
                return (
                  <Image
                    source={require('../../../assets/icons/StoreInfo.png')}
                    style={{
                      width: 25,
                      height: 25,
                    }}
                    resizeMode='contain'
                  />
                );
              }}
            />
          }
          {
            products.map((product) => {
              return (
                <Product
                  key={product.id}
                  product={product}
                  action={() =>
                    navigation.navigate(
                        'ProductDetails', {id: product.idProduct},
                    )}
                />
              );
            })
          }
        </View>
      </ScrollView>
    </View>
  );
};

HomeStoreScreen.propTypes = {
  navigation: PropTypes.any,
};
export default HomeStoreScreen;
