import {
  AddImage,
  Background,
  Button,
  TextArea,
  TextInput,
  Navbar,
} from './../../components/UI';
import {
  Title,
  Text,
  useTheme,
} from 'react-native-paper';
import {
  ScrollView,
  View,
  TouchableOpacity,
} from 'react-native';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';

// Styles
import {newMarketStyles as styles} from './Bussines.styles';

// Utils
import TakeImageClass from '../../config/utils/ImagePreparison';

// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import ShopClass from '../../services/Shop.class';

const NewProductScreen = ({navigation, route}) => {
  const {colors} = useTheme();
  const dispach = useDispatch();
  const [form, setForm] = useState({
    image: [],
    name: '',
    description: '',
    price: '',
  });
  const [errorName, setErrorName] = useState(' ');
  const [errorNumber, setErrorNumber] = useState(' ');
  const [errorDescription, setErrorDescription] = useState(' ');
  useEffect(() => {
    const getProduct = navigation.addListener('focus', () => {
      getDataById();
    });

    return getProduct;
  }, []);

  const changeForm = (key, value) => {
    setForm({
      ...form,
      [key]: value,
    });
  };

  const getImage = async () => {
    const image = await TakeImageClass.getImageByDevice();
    image.title = 'Image_'+form.image.length;
    setForm({
      ...form,
      image: [...form.image, image],
    });
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const removeImage = async (index) => {
    setForm({
      ...form,
      image: form.image.filter((img, i) => i !== index ),
    });
  };

  const validateDecimal = (cadena, caracter) => {
    const indices = [];
    for (let i = 0; i < cadena.length; i++) {
      if (cadena[i].toLowerCase() === caracter) indices.push(i);
    }
    return indices.length;
  };

  const validateNumber = (text) => {
    let newText = '';
    const numbers = '0123456789.';
    for (let i = 0; i < text.length; i++) {
      if (
        numbers.indexOf(text[i]) > -1
      ) {
        newText = newText + text[i];
      } else {
        setErrorNumber('Este campo solo admite numeros');
      }
    };
    const dots = validateDecimal(newText, '.');

    if (dots > 1) {
      return;
    };

    if (newText !== form.price) {
      setErrorNumber(' ');
    };

    if (newText[0] !== '$') {
      newText = '$'+newText;
    };

    changeForm('price', newText);
  };

  const addProduct = async () => {
    try {
      const {info} = route.params;
      const add = new ShopClass();
      dispach({
        layout: {
          layout: true,
        },
        type: RENDER_LAYOUT,
      });
      await window.dataLayer.push({
        event: 'add_product',
        eventProps: {
          category: 'form',
          action: 'submit',
          label: 'add_product',
          value: '',
        },
      });
      await add.addProduct({
        ...form,
        id: info.id,
      });
      setForm({
        image: [],
        name: '',
        description: '',
        price: '',
      });

      navigation.navigate('SuccesProduct', {
        name: form.name,
        routeToShare: info.idShop,
      });
    } catch (e) {
      if (!e.length) {
        dispach({
          message: {
            show: true,
            message: 'Servicio no disponible',
            status: 'N',
          },
          type: RENDER_MESSAGE,
        });
        dispach({
          layout: {
            layout: false,
          },
          type: RENDER_LAYOUT,
        });
        return;
      };
      const errors = {
        name: () => setErrorName('Campos con * son obligatorios'),
        description: () => setErrorDescription('Campos con * son obligatorios'),
        price: () => setErrorNumber('Campos con * son obligatorios'),
      };
      const errorKeys = Object.keys(errors);
      for (const key of errorKeys) {
        if (e.includes(key)) {
          errors[key]();
        };
      };
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  // Edit Actions
  const getDataById = async () => {
    if (!route.params.productId) {
      return;
    }
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      const shop = new ShopClass();
      const product = await
      shop.getOneProductById(route.params.productId);
      const newImages = [];
      for (const image of product.images) {
        const newImage = await TakeImageClass.getEmergencyFoundImg(image);
        newImages.push(newImage);
      }
      setForm({
        image: newImages,
        name: product.productName,
        description: product.productDescription,
        price: '$'+product.price,
        id: product.shop.id,
        idProduct: product.idProduct,
      });
    } catch (e) {
      console.log(e);
      dispach({
        message: {
          show: true,
          message: 'No es posible localizar el producto',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
      navigation.goBack();
    };
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const deleteProductById = async () => {
    const {productId} = route.params;
    const shop = new ShopClass();
    try {
      dispach({
        layout: {
          layout: true,
        },
        type: RENDER_LAYOUT,
      });
      await shop.deleteProductById(productId);
      navigation.navigate('HomeStore');
      dispach({
        message: {
          show: true,
          message: 'Producto Eliminado',
          status: 'S',
        },
        type: RENDER_MESSAGE,
      });
    } catch (e) {
      dispach({
        layout: {
          layout: false,
        },
        type: RENDER_LAYOUT,
      });
      dispach({
        message: {
          show: true,
          message: 'Intentelo mas tarde',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    };
  };
  const alterProduct = async () => {
    const {productId, id, idProduct} = route.params;
    const shop = new ShopClass();
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    try {
      await shop.updateProduct(
          form,
          productId,
      );
      navigation.navigate('SuccessUpdateProduct', {
        name: form.name,
        id: idProduct,
      });
    } catch (e) {
      if (!e.length) {
        dispach({
          message: {
            show: true,
            message: 'Servicio no disponible',
            status: 'N',
          },
          type: RENDER_MESSAGE,
        });
        dispach({
          layout: {
            layout: false,
          },
          type: RENDER_LAYOUT,
        });
        return;
      };
      const errors = {
        name: () => setErrorName('Debes ingresar esta información'),
        description: () =>
          setErrorDescription('Debes ingresar esta Información'),
        price: () => setErrorNumber('Debes ingresar esta información'),
      };
      const errorKeys = Object.keys(errors);
      for (const key of errorKeys) {
        if (e.includes(key)) {
          errors[key]();
        };
      };
    };
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const sendForm = () => {
    if (!route.params.productId) {
      addProduct();
      return;
    };
    alterProduct();
  };
  return (
    <View
      style={[
        styles.container,
      ]}
    >
      <Background/>
      <Navbar
        navigation={navigation}
      />
      <ScrollView
        style={styles.scrollContainer}
        keyboardDismissMode={'interactive'}
        keyboardShouldPersistTaps={'handled'}
        contentContainerStyle={styles.contentContainer}
      >
        <View
          style={styles.interiorContainer}
        >
          <View style={[
            styles.row,
            {flexDirection: 'column'},
          ]}>
            {
              !route.params.productId &&
            <Title
              style={styles.titlesStyle}
            >
               Nuevo Producto
            </Title>
            }
            {
              route.params.productId &&
            <Title
              style={styles.titlesStyle}
            >
               Editar Producto
            </Title>
            }
            <Text
              style={styles.subTitleStyle}
            >
              Agrega información detallada de tu servicio o producto
            </Text>
          </View>
          <View
            style={[
              styles.row,
            ]}
          >
            <ScrollView
              horizontal={true}
            >
              {
                form.image.length < 3 &&
                <AddImage
                  image={null}
                  title={'Imagen'}
                  onAddImage={getImage}
                />
              }
              {
                form.image.map((item, index) => {
                  return (
                    <AddImage
                      key={index}
                      image={item}
                      title={item.title}
                      onAddImage={getImage}
                      removeImage={() => removeImage(index)}
                      disabled={form.image.length == 4}
                    />
                  );
                })
              }
            </ScrollView>
          </View>
          <View
            style={styles.row}
          >
            <TextInput
              value={form.name}
              label='Nombre del producto *'
              placeholder='Ej: Globos decorados'
              maxLength={30}
              onChangeText={(value) => {
                if (
                  value.match(/^[A-Za-zÁÉÍÓÚáéíóúñÑ0-9 ]+$/g) ||
                  value.length == 0
                ) {
                  setErrorName(' ');
                  changeForm('name', value);
                  return;
                };
                setErrorName('Este campo solo acepta letras y numeros');
              }}
              helper={errorName}
              type={errorName == ' ' ? 'primary' : 'danger'}
            />
          </View>
          <View
            style={[
              styles.row,
              {width: '50%'},
            ]}
          >
            <TextInput
              value={form.price}
              label='Precio *'
              placeholder='MXN'
              onChangeText={(value) => validateNumber(value)}
              keyboardType={'numeric'}
              maxLength={10}
              helper={errorNumber}
              type={errorNumber == ' ' ? 'primary' : 'danger'}
            />
          </View>
          <View
            style={styles.row}
          >
            <TextArea
              value={form.description}
              label='Describe tu producto *'
              placeholder='Ej: Aretes de bisuteria con chapa de oro'
              onChangeText={(value) => {
                setErrorDescription(' ');
                changeForm('description', value);
              }}
              maxLength={200}
              type={errorDescription == ' ' ? 'primary' : 'danger'}
              helper={errorDescription}
            />
          </View>
          <View
            style={styles.row}
          >
            <Button
              mode={'buttonsGColors'}
              title={
                !route.params.productId ?
                'Añadir producto' :
                'Actualizar producto'
              }
              onPress={() => {
                sendForm();
              }}
              fullWidth={true}
              disabled={
                errorName !== ' ' ||
                errorNumber !== ' ' ||
                errorDescription !== ' ' ? true :false
              }
            />
          </View>
          {
            route.params.productId &&
              <View
                style={styles.centerStyle}
              >
                <Button
                  type="danger"
                  mode={'buttonsGOutlineColors'}
                  title="Eliminar producto"
                  onPress={deleteProductById}
                  fullWidth={true}
                />
              </View>
          }
          {
            !route.params.productId &&
            <View
              style={styles.centerStyle}
            >
              <Button
                onPress={() => navigation.navigate('HomeStore')}
                fullWidth={true}
                mode={'buttonsGOutlineColors'}
                title={
                  'Cancelar'
                }
              />
            </View>
          }
          <View
            style={{padding: 10}}
          />
        </View>
      </ScrollView>
    </View>
  );
};
NewProductScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};
export default NewProductScreen;
