import {
  Button,
  Checkbox,
  ModalAlert,
  Tabs,
  Navbar,
} from '../../components/UI';
import {
  Title,
  Text,
} from 'react-native-paper';
import {
  ScrollView,
  TouchableOpacity,
  View,
  Image,
} from 'react-native';
import MapView from '../../components/Maps.screen';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';

// Styles
import {newMarketStyles as styles} from './Bussines.styles';

// icons
import {AntDesign} from '@expo/vector-icons';

// Redux
import {RENDER_MESSAGE, RENDER_LAYOUT, AUTH_USER} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import ShopClass from './../../services/Shop.class';
import TakeImageClass from './../../config/utils/ImagePreparison';
// Styles
import StylesVariables from '../../config/Styles/app.style';

const enumValues = {
  5000: {
    latitudeDelta: .11,
    longitudeDelta: .11,
    zoom: 12,
    text: 'Hasta 5 km',
  },
  10000: {
    latitudeDelta: .22,
    longitudeDelta: .22,
    zoom: 11,
    text: 'Hasta 10 km',
  },
  50000: {
    latitudeDelta: .3,
    longitudeDelta: .3,
    zoom: 9,
    text: 'Hasta 50 km',
  },
};

const tabsModel = [
  {
    activeColor: StylesVariables.Kometa,
    inactiveColor: StylesVariables.Asteroide60p,
    optionText: 'Información',
    icon: () => {
      return (
        <Image
          source={require('../../../assets/icons/storeTab.png')}
          style={{
            width: 14,
            height: 14,
            tintColor: '#9FABA9',
          }}
        />
      );
    },
  },
  {
    activeColor: StylesVariables.Kometa,
    inactiveColor: StylesVariables.Asteroide60p,
    optionText: 'Entregas',
    icon: () => {
      return (
        <Image
          source={require('../../../assets/icons/deliveryTab.png')}
          style={{
            width: 16,
            height: 16,
            tintColor: '#00D6AC',
          }}
        />
      );
    },
  },
];

const StepTwoScreen = ({navigation, route}) => {
  useEffect(() => {
    const getData = navigation.addListener('focus', () => {
      getAlter();
    });

    return getData;
  }, [navigation]);
  const [radius, setRadius] = useState(0);
  const dispach = useDispatch();
  const [recoger, setRecoger] = useState('checked');
  const [mapZoom, setMapZoom] = useState({
    latitudeDelta: .11,
    longitudeDelta: .11,
    zoom: 12,
    text: 'Hasta 5 km',
  });
  const [localModal, setLocalModal] = useState(false);
  const buttonsModal = [
    {
      text: 'Sí, continuar',
      action: () => navigation.goBack(),
      mode: 'buttonsOutlineColors',
    },
    {
      text: 'Regresar',
      action: () => setLocalModal(false),
      mode: 'buttonsColors',
    },
  ];

  const changeRadius = (action) => {
    if (action == 'add') {
      if (radius >= 50000) {
        return;
      };
      const enumSum = {
        5000: {
          latitudeDelta: .22,
          longitudeDelta: .22,
          radius: 10000,
          zoom: 11,
          text: 'Hasta 10 km',
        },
        10000: {
          latitudeDelta: .3,
          longitudeDelta: .3,
          radius: 50000,
          zoom: 9,
          text: 'Hasta 50 km',
        },
      };
      setRadius(enumSum[radius].radius);
      setMapZoom({
        latitudeDelta: enumSum[radius].latitudeDelta,
        longitudeDelta: enumSum[radius].longitudeDelta,
        zoom: enumSum[radius].zoom,
        text: enumSum[radius].text,
      });
      return;
    };

    if (radius <= 5000 ) {
      return;
    }
    const enumRest = {
      50000: {
        latitudeDelta: .22,
        longitudeDelta: .22,
        radius: 10000,
        zoom: 11,
        text: 'Hasta 10 km',
      },
      10000: {
        latitudeDelta: .11,
        longitudeDelta: .11,
        radius: 5000,
        zoom: 12,
        text: 'Hasta 5 km',
      },
    };
    setRadius(enumRest[radius].radius);
    setMapZoom({
      latitudeDelta: enumRest[radius].latitudeDelta,
      longitudeDelta: enumRest[radius].longitudeDelta,
      zoom: enumRest[radius].zoom,
      text: enumRest[radius].text,
    });
    return;
  };

  // Alter Actions
  const getAlter = () => {
    if (route.name !== 'AlterStepTwo') {
      return;
    };
    const {info} = route.params;
    setRadius(info.homeDelivery);
    setRecoger(info.storeDelivery ? 'checked' : 'unchecked');
    if (info.homeDelivery !== 0) {
      setMapZoom(
          enumValues[info.homeDelivery],
      );
    };
  };

  const alterStore = async () => {
    try {
      const {info} = route.params;
      dispach({
        layout: {
          layout: true,
        },
        type: RENDER_LAYOUT,
      });
      let img = '';
      if (info.image) {
        img = await TakeImageClass.formatImageArr([info.image]);
        img = img[0];
      };
      const shopClass = new ShopClass();
      const userData = {
        shopName: info.name,
        phoneNumber: info.number,
        address: info.address,
        latitude: info.latitude,
        longitude: info.longitude,
        shopDescription: info.description,
        category: info.category._id,
        image: img,
        storeDelivery: recoger == 'checked' ? true : false,
        homeDelivery: radius,
      };
      await shopClass.alterShop(
          userData,
          info.id,
      );
      const userInfo = await shopClass.getShopById(info.id);
      dispach({
        userInfo: {
          number: userData.phoneNumber,
          userInfo: userInfo,
        },
        type: AUTH_USER,
      });
      if (route.name == 'AlterStepTwo') {
        navigation.navigate('HomeStore');
        dispach({
          message: {
            show: true,
            message: 'Tienda Editada',
            status: 'S',
          },
          type: RENDER_MESSAGE,
        });
      } else {
        dispach({
          layout: {
            layout: false,
          },
          type: RENDER_LAYOUT,
        });
        navigation.navigate('SuccesForm', {
          info: userData,
        });
      };
    } catch (e) {
      console.log(e);
      dispach({
        message: {
          show: true,
          message: 'En este momento no podemos procesar tu solicitud',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
      dispach({
        layout: {
          layout: false,
        },
        type: RENDER_LAYOUT,
      });
    };
  };

  const sendForm = () => {
    alterStore();
  };
  return (
    <View
      style={[
        styles.container,
      ]}
    >
      <Navbar
        navigation={navigation}
      />
      <ScrollView
        style={styles.scrollContainer}
        keyboardDismissMode={'interactive'}
        keyboardShouldPersistTaps={'handled'}
        contentContainerStyle={styles.contentContainer}
      >
        <View
          style={styles.interiorContainer}
        >
          {
            route.name !== 'AlterStepTwo' &&
              <View style={[
                styles.row,
                {flexDirection: 'column'},
              ]}>
                <Title
                  style={styles.titlesStyle}
                >
                  Tipo de entrega
                </Title>
                <Text
                  style={styles.subTitleStyle}
                >
                  ¿Cómo entregarás tus pedidos?
                </Text>
              </View>
          }
          {
            route.name == 'AlterStepTwo' &&
              <View style={[
                styles.row,
                {flexDirection: 'column'},
              ]}>
                <Title
                  style={styles.titlesStyle}
                >
                  Editar tienda
                </Title>
                <Tabs
                  tabs={tabsModel}
                  current={1}
                  changeTab={() => {
                    setLocalModal(true);
                  }}
                  disableCurrent={1}
                />
              </View>
          }
          <View
            style={[
              styles.row,
              {flexDirection: 'row', alignItems: 'center'},
            ]}
          >
            <Checkbox
              status={recoger}
              onPress={() => {
                if (radius <= 0) {
                  return;
                }
                if (recoger == 'checked') {
                  setRecoger('unchecked');
                  return;
                };
                setRecoger('checked');
              }}
              helper={route.params.info.address}
              label={'Entregar en mi tienda'}
            />
          </View>
          <View
            style={[
              styles.row,
              {flexDirection: 'row', alignItems: 'center'},
            ]}
          >
            <Checkbox
              status={radius > 0 ? 'checked' : 'unchecked'}
              onPress={() => {
                if (recoger == 'unchecked') {
                  return;
                };
                if (radius > 0) {
                  setRadius(0);
                  return;
                };
                setRadius(5000);
              }}
              label={'Entrega a domicilio'}
              helper={enumValues[radius]?.text}
            />
          </View>
          {
            radius > 0 &&
            <View
              style={styles.row}
            >
              <Text
                style={
                  styles.italicFont
                }
              >
                Elige la distancia que puedes abarcar, presionando los
                botones para modificarla
              </Text>
            </View>
          }
          {
            radius > 0 &&
              <View
                style={styles.mapContainer}
              >
                <MapView
                  latitude={route.params.info.latitude}
                  longitude={route.params.info.longitude}
                  changeAddress={() => {}}
                  radius={radius}
                  latitudeDelta={mapZoom.latitudeDelta}
                  longitudeDelta={mapZoom.longitudeDelta}
                  zoom={mapZoom.zoom}
                  editable={false}
                />
                <View
                  style={styles.mapCover}
                />
              </View>
          }
          {
            radius > 0 &&
              <View
                style={styles.buttonsMapContainer}
              >
                <TouchableOpacity
                  style={{
                    marginHorizontal: 8,
                  }}
                  onPress={() => changeRadius('remove')}
                >
                  <View
                    style={styles.buttonsRadius}
                  >
                    <AntDesign
                      name="minus"
                      size={24}
                      color={styles.iconColor.color}
                    />
                  </View>
                </TouchableOpacity>
                <Text
                  style={styles.radiusText}
                >
                  {mapZoom.text}
                </Text>
                <TouchableOpacity
                  style={{
                    marginHorizontal: 8,
                  }}
                  onPress={() => changeRadius('add')}
                >
                  <View
                    style={styles.buttonsRadius}
                  >
                    <AntDesign
                      name="plus"
                      size={24}
                      color={styles.iconColor.color}
                    />
                  </View>
                </TouchableOpacity>
              </View>
          }
          <View
            style={styles.row}
          >
            <Button
              title={
                route.name !== 'AlterStepTwo' ? 'Siguiente':
                'Actualizar Tienda'
              }
              onPress={sendForm}
              fullWidth={true}
              mode={'buttonsGColors'}
            />
          </View>
          {
            route.name == 'AlterStepTwo' &&
            <View
              style={styles.row}
            >
              <View
                style={{padding: 10}}
              />
              <Button
                onPress={() => navigation.navigate('HomeStore')}
                fullWidth={true}
                mode={'buttonsGOutlineColors'}
                title={
                  'Cancelar'
                }
              />
            </View>
          }
          <View
            style={{padding: 10}}
          />
        </View>
      </ScrollView>
      <ModalAlert
        isVissible={localModal}
        title={'¿Estás seguro de continuar?'}
        message={'Los cambios que realizaste se podrían perder'}
        buttons={buttonsModal}
        onClose={() => setLocalModal(false)}
      />
    </View>
  );
};

StepTwoScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.object,
};
export default StepTwoScreen;
