import {RENDER_MESSAGE} from '../constants';

const initialState = {
  show: false,
  message: '',
  status: 'N',
};

const messageReducer = ( state = initialState, action ) => {
  switch (action.type) {
    case RENDER_MESSAGE:
      return action.message;
    default:
      return state;
  };
};

export default messageReducer;
