import {
  Button,
  ShopModal,
  ProductsBottomTab,
  SquareMinButton,
  MapModal,
  Background,
  Logo as StoreLogo,
} from './../../components/UI';
import {Card, Text, Paragraph} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';
import {buyAction} from '../../config/utils/shareClass';
import {
  Image,
  ImageBackground,
  ScrollView,
  View,
  TouchableOpacity,
  Platform,
} from 'react-native';
import Product from '../../components/Product';
import {defaultHeader} from '../../config/utils/objectForlogo';

// Styles
import {homeStoreStyles as styles} from './home.styles';
import StylesVariables from '../../config/Styles/app.style';

// Icons
import {AntDesign, MaterialCommunityIcons} from '@expo/vector-icons';

// redux
import {RENDER_LAYOUT, RENDER_MESSAGE, AUTH_USER} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Classes
import ShopClass from '../../services/Shop.class';
import GeocodingClass from '../../config/utils/GeolocationFunctions';

const HomeStoreScreen = ({navigation, route}) => {
  useEffect(() => {
    const getProducts = navigation.addListener('focus', () => {
      getAllStore();
      getUserLocale();
    });
    return getProducts;
  }, []);

  const dispach = useDispatch();
  const [products, setProducts] = useState([]);
  const [storeData, setStoreData] = useState({
    shopName: '',
    address: '',
    latitude: 0,
    longitude: 0,
  });
  const [shoppingCart, setShoppingCart] = useState([]);
  const [infoModal, setInfoModal] = useState(false);
  const [mapModal, setMapModal] = useState(false);
  const [userLocale, setUserLocale] = useState();
  const [currentUser, setCurrentUser] = useState(false);
  // shareAction
  const [formUser, setFormUser] = useState({
    name: '',
    phone: '',
  });

  const getCurrentUser = async () => {
    let token;
    if (Platform.OS === 'web') {
      token = localStorage.getItem('kometa-token');
    } else {
      token = await AsyncStorage.getItem('kometa-token');
    };
    let userInfo = {};
    if (token) {
      const getInfo = new ShopClass();
      userInfo = await getInfo.getShopByNumber(token);
      setCurrentUser(userInfo);
    };
  };

  const changeStackToMyStore = () => {
    dispach({
      userInfo: {
        number: currentUser.phoneNumber,
        userInfo: currentUser,
        costumer: 'no_costumer',
      },
      type: AUTH_USER,
    });
    return;
  };

  const getUserLocale = async () => {
    try {
      const locale = await GeocodingClass.getCurrentPosition();
      setUserLocale(locale);
    } catch (e) {
      console.log(e);
    };
  };

  const getDistance = (lat1, lng1, lat2, lng2) => {
    const distance = GeocodingClass.getRadiusKilometer(
        lat1, lng1, lat2, lng2,
    );
    return ` ${Math.ceil(distance)} km`;
  };

  const getAllStore = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    await getCurrentUser();
    try {
      const {idShop} = route.params;
      const shop = new ShopClass();
      const store = await shop.getShopByShareId(idShop);
      const allProducts = await shop.getAllProductsById(store.id);
      if (!store.banner) {
        for (const header of defaultHeader) {
          if (header.types.includes(store.category.description.trim())) {
            store.banner = header.src;
            break;
          };
        };
      } else {
        store.banner = {uri: store.banner};
      };
      setStoreData(store);
      setProducts(allProducts);
    } catch (e) {
      dispach({
        message: {
          show: true,
          message: 'No se puede acceder a su solicitud de momento',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };

  const getUnities = (id) => {
    const unityes = shoppingCart.filter( (item) => item.id === id);
    if (unityes.length > 0) {
      return unityes[0].unit;
    };
    return 0;
  };

  const manageCart = (product, action) => {
    const unityes = shoppingCart.filter( (item) => item.id === product.id);
    const newArr = shoppingCart.filter( (item) => item.id !== product.id);
    if (action === 'dimis') {
      if (unityes.length == 0) {
        return;
      };
      const newObj = {
        ...unityes[0],
        unit: unityes[0].unit - 1,
      };
      if (newObj.unit <= 0) {
        setShoppingCart(newArr);
        return;
      };
      setShoppingCart([...newArr, newObj]);
      return;
    };
    setShoppingCart([
      ...newArr,
      {
        ...product,
        unit: unityes.length == 0 ? 1 :
        unityes[0].unit + 1,
      },
    ]);
  };

  const getAllUnityes = () => {
    let u = 0;
    for (const item of shoppingCart) {
      u = u + item.unit;
    };
    return u;
  };
  const getTotal = () => {
    let u = 0;
    for (const item of shoppingCart) {
      u = u + item.price * item.unit;
    };
    return `Total $${u}.00`;
  };


  const changeForm = (key, value) => {
    setFormUser({
      ...formUser,
      [key]: value,
    });
  };

  const iLikeUStore = () => {
    const message =
    `Hola! soy ${formUser.name}
    Vi tu tienda en kometa.shop, me interesa mucho!
    Me podrías compartir más informes?`;
    buyAction(
        message,
        '',
        '52'+storeData.phoneNumber,
    );
    setInfoModal(false);
  };

  const EndFooter = () => {
    const linkButton = {
      cursor: 'pointer',
      color: '#00d6ac',
      fontFamily: 'Bold',
      fontSize: 16 * StylesVariables.responsiveHeightMulti,
      textDecorationLine: 'underline',
    };

    const buttonKometa = {
      backgroundColor: 'transparent',
      border: '0px solid rgba(0, 0, 0, 0.05)',
    };
    return (
      <View
        style={styles.makeStoreContainer}
      >
        <Image
          style={{width: 30, height: 30}}
          source={require('../../../assets/icons/store.png')}
        />
        <Text
          style={styles.textMakeStore}
        >
          {'  Crea tu propia tienda en'}
        </Text>
        <button
          id='create_store_from_bottom_store'
          style={buttonKometa}
          onClick={changeStackToMyStore}
        >
          <a
            style={linkButton}
          >
            Kometa.shop
          </a>
        </button>
      </View>
    );
  };

  return (
    <View
      style={styles.container}
    >
      <Background/>
      <ScrollView
        style={[
          styles.scrollContainer,
        ]}
        contentContainerStyle={styles.contentContainer}
      >
        <View
          style={styles.responsiveContainer}
        >
          <View
            style={styles.banner}
          >
            {
              storeData.banner &&
                <ImageBackground
                  source={storeData.banner}
                  style={{
                    width: '100%',
                    height: '100%',
                    position: 'absolute',
                  }}
                />
            }
            <View
              style={styles.bannerInterior}
            >
              <View
                style={{flex: 1, alignItems: 'flex-end'}}
              >
                {
                  currentUser &&
                  <SquareMinButton
                    Icon={() => {
                      return (
                        <Image
                          source={require('../../../assets/icons/irhome.png')}
                          style={{width: 15, height: 15}}
                        />
                      );
                    }}
                    colorButton='white'
                    onPress={changeStackToMyStore}
                  />
                }
                {
                  !currentUser &&
                    <Button
                      type={'white'}
                      title={'Mi Tienda'}
                      id={'make_store_from_store'}
                      onPress={changeStackToMyStore}
                    />
                }
              </View>
            </View>
          </View>
        </View>
        <View
          style={styles.storeContainer}
        >
          <Card>
            <Card.Content
              style={styles.profileContainer}
            >
              <View
                style={{flex: .3}}
              >
                {
                  storeData.image == '' &&
                    <StoreLogo
                      category={storeData.category.description}
                      name={storeData.shopName}
                    />
                }
                {
                  storeData.image !== '' &&
                    <View
                      style={styles.pictureContainer}
                    >
                      <ImageBackground
                        source={{uri: storeData.image}}
                        style={{width: '100%', height: '100%'}}
                      />
                    </View>
                }
              </View>
              <View
                style={{flex: .7}}
              >
                <View
                  style={styles.titleContainer}
                >
                  <Text
                    style={styles.titleProfile}
                  >
                    {storeData.shopName}
                  </Text>
                </View>
                <View
                  style={styles.titleContainer}
                >
                  <Paragraph
                    style={styles.locationText}
                  >
                    {storeData.address}
                  </Paragraph>
                  {
                    storeData.homeDelivery !== 0 &&
                    storeData.homeDelivery &&
                    <Paragraph
                      style={styles.locationText}
                    >
                      <MaterialCommunityIcons
                        name="home-outline"
                        style={styles.locationIcon}
                      />
                      {' Envios a domicilio '}
                      <Text
                        style={styles.locationKilometer}
                      >
                        {`${storeData.homeDelivery / 1000}km`}
                      </Text>
                    </Paragraph>
                  }
                  {
                    storeData.storeDelivery !== 0 &&
                    <Paragraph
                      style={styles.locationText}
                    >
                      <TouchableOpacity
                        onPress={() => setMapModal(true)}
                      >
                        <MaterialCommunityIcons
                          name="storefront-outline"
                          style={styles.locationIcon}
                        />
                      </TouchableOpacity>
                      {' Recoger en tienda'}
                      {
                        userLocale &&
                        <Text
                          style={styles.locationKilometer}
                        >
                          {
                            `${
                              getDistance(
                                  userLocale.latitude,
                                  userLocale.longitude,
                                  storeData.latitude,
                                  storeData.longitude,
                              )
                            }
                            `
                          }
                        </Text>
                      }
                    </Paragraph>
                  }
                </View>
              </View>
            </Card.Content>
          </Card>
          <Text
            style={[
              styles.titleProfile,
              {marginVertical: 30},
            ]}
          >
            Productos
          </Text>
          {
            products.map((product) => {
              return (
                <Product
                  key={product.id}
                  product={product}
                  client={true}
                  items={getUnities(product.id)}
                  action={() => {
                    navigation.navigate('ProductDetails', {
                      productId: product.idProduct,
                      store: storeData,
                    });
                  }}
                  addCartAction={manageCart}
                />
              );
            })
          }
        </View>
        {
          !currentUser &&
            <EndFooter/>
        }
      </ScrollView>
      <View
        style={styles.contentContainer}
      >
        <View
          style={styles.responsiveContainer}
        >
          {
            shoppingCart.length == 0 &&
            !infoModal &&
            <View
              style={styles.bottomButtonContainer}
            >
              <Button
                title={'Realizar pedido por WhatsApp'}
                fullWidth={true}
                mode={'buttonsGColors'}
                onPress={() => setInfoModal(true)}
              />
            </View>
          }
          {
            infoModal &&
            shoppingCart.length == 0 &&
            !mapModal &&
              <ShopModal
                isOpen={infoModal}
                closeModal={() => {
                  setInfoModal(false);
                }}
                sendShopCart={iLikeUStore}
                changeForm={changeForm}
                name={formUser.name}
                phoneNumber={formUser.phone}
              />
          }
          {
            !infoModal &&
            shoppingCart.length >= 1 &&
            !mapModal &&
              <ProductsBottomTab
                products={() => getAllUnityes()}
                total={getTotal}
                onBuy={() => navigation.navigate('CarInfo', {
                  car: shoppingCart,
                  store: storeData,
                })}
              />
          }
          {
            mapModal &&
            <MapModal
              isOpen={mapModal}
              latitudeStore={parseFloat(storeData.latitude)}
              longitudeStore={parseFloat(storeData.longitude)}
              closeModal={() => setMapModal(false)}
            />
          }
        </View>
      </View>
    </View>
  );
};

HomeStoreScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};
export default HomeStoreScreen;
