import PropTypes from 'prop-types';
import React, {useEffect, useState} from 'react';
import {FlatList, Text, View} from 'react-native';
import {LinearGradient} from 'expo-linear-gradient';

// Components
import {Button, MenuItem, Background} from '../../components/UI';
import Logo from './../../components/Logo';
// Styles
import {recursosStyles as styles, gradient} from './home.styles';
// redux
import {useSelector, useDispatch} from 'react-redux';
import {authAndDrive} from '../../redux/actions/userActions';
import {AUTH_USER} from '../../redux/constants';

const menu = [
  {
    title: 'Tu tienda',
    subTitle: 'Empieza a crear tu tienda',
    active: false,
    icon: require('./../../../assets/MenuIcons/store.png'),
    key: 'tienda',
  },
  {
    title: 'Almacén',
    subTitle: 'Próximamente',
    active: false,
    icon: require('./../../../assets/MenuIcons/trash.png'),
    key: 'almacen',
  },
  {
    title: 'Aula',
    subTitle: 'Próximamente',
    active: false,
    icon: require('./../../../assets/MenuIcons/book.png'),
    key: 'aula',
  },
];
const Recursos = ({navigation}) => {
  const [resources, setResources] = useState(menu);
  const [loading, setLoading] = useState(true);
  const dispach = useDispatch();
  const {user} = useSelector(
      (state) => ({
        user: authAndDrive(state),
      }),
  );
  const userData = user.userInfo.user.userInfo;
  useEffect(() => {
    const getButtons = navigation.addListener('focus', () => {
      getButtonState();
    });
    return getButtons;
  }, []);

  useEffect(() => {
    if (userData.newStore) {
      navigation.navigate('ManageMarket', {
        screen: 'NewMarket',
        params: {
          number: userData.shopNumber,
          storeId: userData.id,
        },
      });
      dispach({
        userInfo: {
          number: userData.phoneNumber,
          userInfo: {
            ...userData,
          },
        },
        type: AUTH_USER,
      });
    };
  }, [loading]);

  const getButtonState = () => {
    const newMenu = [...resources];
    const finalMenu = [];
    for (const item of newMenu) {
      item.active = inactiveButtons[item.key];
      finalMenu.push(item);
    };
    setResources(finalMenu);
    setLoading(false);
  };
  const inactiveButtons = {
    tienda: true,
    almacen: false,
    aula: false,
  };
  const changeRoute = (key) => {
    switch (key) {
      case 'tienda':
        if (!userData.shopName) {
          navigation.navigate('ManageMarket', {
            screen: 'NewMarket',
            params: {
              number: userData.shopNumber,
              storeId: userData.id,
            },
          });
          return;
        };
        navigation.navigate('HomeStore');
        break;
    };
  };
  return (
    <View
      style={styles.container}
    >
      <Background/>
      <View
        style={styles.responsiveContainer}
      >
        <View
          style={styles.top}
        >
          <View
            style={styles.row}
          >
            <Logo/>
            <Text
              style={styles.titlesStyle}
            >
              Mis recursos
            </Text>
            <Text
              style={styles.subTitleStyle}
            >
              Herramientas disponibles para
              mejorar tu negocio
            </Text>
          </View>
          <FlatList
            showsVerticalScrollIndicator={false}
            data={resources}
            renderItem={({item}, index) => {
              return (
                <MenuItem
                  title={item.title}
                  subTitle={item.subTitle}
                  active={item.active}
                  icon={item.icon}
                  loading={loading}
                  key={index}
                  route={item.route}
                  onPress={() => changeRoute(item.key)}
                />
              );
            }}
            style={{height: 10}}
          />
        </View>
        <View
          style={styles.bottom}
        >
          <View
            style={styles.bottomContainer}
          >
            <LinearGradient
              colors={gradient}
              style={styles.decorationContainer}
              start={{x: 0, y: 0.5}}
              end={{x: 1, y: 0.5}}
            />
            <View
              style={{flex: 1}}
            >
              <Text
                style={styles.products}
              >
                {'Responde y gana $30'}
              </Text>
            </View>
            <View
              style={{flex: 1, alignItems: 'flex-end'}}
            >
              <Button
                title="Ayudar"
                mode="buttonsGColors"
                type="white"
                onPress={() => {
                  navigation.navigate('ayudanos');
                }}
              />
            </View>
          </View>
        </View>
      </View>
    </View>
  );
};

Recursos.propTypes = {
  navigation: PropTypes.any,
};
export default Recursos;
