import * as ImagePicker from 'expo-image-picker';
import {manipulateAsync, SaveFormat} from 'expo-image-manipulator';
import {Platform} from 'react-native';
/**
 * ImagePreparison centraliza la garga de imagenes y te regresa un base64
 */
class ImagePreparisson {
  /**
   * Obtiene la imagen desde el dispositivo
   * @return {promise}
   */
  getImageByDevice() {
    return new Promise(async (resolve, reject) => {
      const permissionResult =
        await ImagePicker.requestMediaLibraryPermissionsAsync();
      if (!permissionResult.granted) {
        reject(
            new Error('Necesitamos tus permisos para acceder a esta función.'),
        );
      };
      const photo = await ImagePicker.launchImageLibraryAsync({
        mediaTypes: ImagePicker.MediaTypeOptions.Images,
        allowsEditing: false,
        aspect: [4, 3],
        quality: 1,
        base64: true,
      });
      if (!photo) {
        resolve(false);
        return;
      };
      const img =
        Platform.OS !== 'web' ? photo.base64 :
        `data:image/jpeg;base64,${photo.base64}`;
      const image = {
        uri: img,
        type: photo.type,
        name: photo.name,
        width: photo.width,
        height: photo.height,
      };
      resolve(image);
    });
  };
  /**
   * @parametros {string} base64
   * Represents a parametros.
   * Crea un array de imagenes en base64
   * disminuyendo su peso para enviarlo al backend
   * @param {string} image un base64
   * @return{promise}
  */
  resizeImage(image) {
    return new Promise(async (resolve, reject) => {
      const manipulate = await manipulateAsync(
          image.uri,
          [
            {
              resize: {
                width: image.width / 2,
                height: image.height / 2,
              },
            },
          ],
          {
            compress: .5,
            format: SaveFormat.JPEG,
            base64: true,
          },
      );
      resolve({
        uri: manipulate.base64,
        width: manipulate.width,
        height: manipulate.height,
      });
    });
  };
  /**
   * @parametros {array} array de imagenes
   * Represents a parametros.
   * Crea un array de imagenes en base64
   * disminuyendo su peso para enviarlo al backend
   * @param {array} imageList una lista de imagenes
   * que puede ser un base64 o un objeto de imagen
   * @return{promise}
  */
  formatImageArr(imageList) {
    return new Promise(async (resolve) => {
      const newArr = [];
      for (const image of imageList) {
        let img = image;
        if (!image.uri) {
          const imgDimentions = new Image();
          imgDimentions.src = image;
          img = {
            uri: image,
            width: imgDimentions.width,
            height: imgDimentions.height,
          };
        }
        let buffer = Buffer.from(img.uri.substring(img.uri.indexOf(',') + 1));
        let mb = buffer.length / 1e+6;
        while (mb >= 1.5) {
          img = await this.resizeImage(img);
          buffer = Buffer.from(img.uri.substring(img.uri.indexOf(',') + 1));
          mb = buffer.length / 1e+6;
        };
        newArr.push(img.uri);
      };
      resolve(newArr);
    });
  };
  /**
   * Abre la camara para obtener le foto
   * @return {promise}
   */
  getImageByCamera() {
    return new Promise(async (resolve, reject) => {
      const permissionResult =
        await ImagePicker.getCameraPermissionsAsync();
      if (!permissionResult) {
        reject(
            new Error('Necesitamos tus permisos para acceder a esta función'),
        );
      };
      const photo = await ImagePicker.launchCameraAsync({
        mediaTypes: ImagePicker.MediaTypeOptions.Images,
        allowsEditing: false,
        aspect: [4, 3],
        quality: 1,
        base64: false,
      });
      if (photo.cancelled) {
        resolve();
        return;
      }

      const manipulate = await manipulateAsync(
          photo.uri,
          [],
          {
            compress: .4,
            format: SaveFormat.JPEG,
            base64: true,
          },
      );
      const image =
        Platform.OS == 'web' ? manipulate.base64 :
        `data:image/jpeg;base64, ${manipulate.base64}`;

      resolve({
        image,
        name: '',
      });
    });
  };
  /**
   * @parametros {string} string de imagen
   * Genera un base64 en base a la
   * url de la imagen
   * @param {string} urlImg una url de imagen
   * a convertir en base64
   * @return {promise}
   */
  getEmergencyFoundImg(urlImg) {
    return new Promise(async (resolve) => {
      const manipulate = await manipulateAsync(
          urlImg,
          [],
          {
            compress: 1,
            format: SaveFormat.JPEG,
            base64: true,
          },
      );
      const img =
        Platform.OS == 'web' ? manipulate.base64 :
        `data:image/jpeg;base64, ${manipulate.base64}`;
      resolve(img);
    });
  };
};

export default new ImagePreparisson();
