import PropTypes from 'prop-types';
import React from 'react';
import {View, StyleSheet} from 'react-native';
import {ActivityIndicator} from 'react-native-paper';
import StylesVariables from '../config/Styles/app.style';
const Loading = (props) => {
  const {show} = props;

  if (!show) return null;

  return (
    <View
      style={styles.container}
    >
      <ActivityIndicator
        animating={true}
        size={'large'}
        color={StylesVariables.Kometa}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'absolute',
    width: '100%',
    height: '100%',
    backgroundColor: 'black',
    opacity: .6,
    alignItems: 'center',
    justifyContent: 'center',
    overflow: 'hidden',
    flex: 1,
  },
});

Loading.propTypes = {
  show: PropTypes.bool,
};

export default Loading;
