import {Animated, View} from 'react-native';
import {Background, Button} from './../../components/UI';
import {Text} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useRef} from 'react';
import {shareAction} from '../../config/utils/shareClass';


// Styles
import {authStyles} from './../auth/auth.styles';
import StylesVariables from './../../config/Styles/app.style';
const SuccesProductScreen = ({navigation, route}) => {
  useEffect(() => {
    setTimeout(() => {
      fadeIn();
    }, 1000);
  }, []);
  const fadeAnim = useRef(new Animated.Value(0)).current;

  const fadeIn = () => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 2000,
    }).start();
  };

  const share = () => {
    shareAction(
        'Hola! te invito a conocer '+route.params.name+' entrando a',
        `mitienda/${route.params.routeToShare}`,
    );
  };
  return (
    <View
      style={[
        authStyles.interiorContainer,
        {backgroundColor: 'transparent'},
      ]}
    >
      <Background/>
      <View
        style={authStyles.responsiveContainer}
      >
        <Animated.View
          style={{opacity: fadeAnim}}
        >
          <Text
            style={[
              authStyles.cardTitle,
              {color: StylesVariables.SanLuis},
            ]}
          >
            Has creado tu producto con éxito
          </Text>
          <Text
            style={[
              authStyles.textStyle,
              {color: StylesVariables.Bajio80p},
            ]}
          >
            {
              'Empieza a compartirlo con tus clientes'
            }
          </Text>
          <View
            style={authStyles.buttonContainer}
          >
            <Button
              uppercase={false}
              onPress={() => {
                share();
              }}
              id={'share_my_new_product'}
              title='Compartir producto'
              fullWidth
              mode={'buttonsGColors'}
            />
            <View
              style={{padding: 10}}
            />
            <Button
              uppercase={false}
              onPress={() => {
                navigation.navigate('HomeStore');
              }}
              title='Continuar'
              fullWidth
              mode={'buttonsGOutlineColors'}
            />
          </View>
        </Animated.View>
      </View>
    </View>
  );
};

SuccesProductScreen.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};

export default SuccesProductScreen;
