import Constants from 'expo-constants';
import {
  GoogleMap,
  Marker,
  withScriptjs,
  withGoogleMap,
  Circle,
} from 'react-google-maps';
import PropTypes from 'prop-types';
import React from 'react';
import {Platform} from 'react-native';
const apiKey = Constants.manifest.extra.geoApi;
import {StyleSheet, Text, View} from 'react-native';
import GooglePlacesAutocompleteWeb from 'react-google-autocomplete';

// Styles
import StylesVariables from '../config/Styles/app.style';

// Icons
import {MaterialCommunityIcons} from '@expo/vector-icons';
const Mapp = ({
  latitude,
  longitude,
  changeAddress,
  radius,
  zoom,
  address,
  changeAddressText,
  completeAddress,
  editable,
  errorMessage,
}) => {
  return (
    <>
      {
        editable &&
          <View
            style={styles.inputContainer}
          >
            <GooglePlacesAutocompleteWeb
              style={
                {
                  ...stylesWeb.input,
                  border:
                    errorMessage == ' ' ?
                      `1px solid ${StylesVariables.AsteroideDark}`:
                      `1px solid ${StylesVariables.Irapuato}`,
                }
              }
              placeholder='Ej. Av. Siempre Viva'
              value={address}
              onChange={(value) => {
                changeAddressText(value.target.value);
              }}
              onPlaceSelected={(place) => {
                changeAddress(
                    place.geometry.location.lat(),
                    place.geometry.location.lng(),
                );
              }}
              options={{
                types: ['(regions)'],
                componentRestrictions: {country: 'mx'},
              }}
            />
            <View
              style={styles.iconContainer}
            >
              <View
                style={styles.interiorIcon}
              >
                <MaterialCommunityIcons
                  name="map-marker-outline"
                  size={25 * StylesVariables.responsiveHeightMulti}
                  color={StylesVariables.Kometa}
                />
              </View>
            </View>
            <View
              style={{
                width: '100%',
                right: 8,
              }}
            >
              <Text
                style={[
                  {
                    ...styles.helper,
                    color:
                      errorMessage == ' ' ? StylesVariables.AsteroideDark :
                        StylesVariables.Irapuato,
                  },
                ]}
              >
                {errorMessage}
              </Text>
            </View>
          </View>
      }
      <View
        style={{marginBottom: 30}}
      >
      </View>
      <GoogleMap
        onClick={(e) => {
          changeAddress(e.latLng.lat(), e.latLng.lng());
        }}
        zoom={!zoom ? 20 : zoom}
        center={{lat: latitude, lng: longitude}}
      >
        <Marker
          position={{
            lat: latitude,
            lng: longitude,
          }}
          icon={{
            url: require('../../assets/icons/storeLocation.png'),
            scaledSize: new google.maps.Size(50, 50),
          }}
        />
        {
          radius &&
            <Circle
              radius={radius}
              center={{
                lat: latitude,
                lng: longitude,
              }}
              options={{
                fillColor: StylesVariables.Kometa80p,
                strokeColor: 'transparent',
              }}
            >
            </Circle>
        }
      </GoogleMap>
    </>
  );
};

const WrapperMap = withScriptjs(withGoogleMap(Mapp));
const MapComponent = ({
  latitude,
  longitude,
  changeAddress,
  radius,
  latitudeDelta,
  longitudeDelta,
  zoom,
  address,
  changeAddressText,
  completeAddress,
  editable,
  errorMessage = ' ',
}) => {
  if (Platform.OS === 'web') {
    return (
      <WrapperMap
        loadingElement={<div style={{height: `100%`}} />}
        googleMapURL={
          'https://maps.googleapis.com/maps/api/js?key='+apiKey+'&libraries=geometry,drawing,places'
        }
        containerElement={<div style={{
          height: `400px`,
          width: '100%',
          display: 'flex',
          flexDirection: 'column-reverse',
        }}/>}
        mapElement={<div style={{height: `100%`}}/>}
        latitude={latitude}
        longitude={longitude}
        changeAddress={changeAddress}
        radius={radius}
        zoom={zoom}
        address={address}
        changeAddressText={changeAddressText}
        completeAddress={completeAddress}
        editable={editable}
        errorMessage={errorMessage}
      >
      </WrapperMap>
    );
  };
  /*
  return (
    <MapView
      region={{
        latitude: latitude,
        longitude: longitude,
        latitudeDelta: !latitudeDelta ? 0.0922 : latitudeDelta,
        longitudeDelta: !longitudeDelta ? 0.0922 : longitudeDelta,
      }}
      style={{
        width: '100%',
        height: '100%',
      }}
      onPress={(e) => {
        changeAddress(
            e.nativeEvent.coordinate.latitude,
            e.nativeEvent.coordinate.longitude,
        );
      }}
    >
      {
        radius &&
          <MapView.Circle
            radius={radius}
            center={{
              latitude: latitude,
              longitude: longitude,
            }}
            fillColor = {'rgba(72,199,116,0.5)'}
          >
          </MapView.Circle>
      }
      <MapView.Marker
        coordinate={{
          latitude: latitude,
          longitude: longitude,
        }}
        title={'Mi tienda'}
      />
    </MapView>
  );
  */
};
const styles = StyleSheet.create({
  inputContainer: {
    paddingHorizontal:
      8 * StylesVariables.responsiveHeightMulti,
    alignItems: 'center',
  },
  iconContainer: {
    width: '100%',
    alignItems: 'flex-end',
  },
  icon: {
    position: 'absolute',
  },
  interiorIcon: {
    backgroundColor: 'white',
    position: 'absolute',
    bottom: 13,
    right: -10,
  },
  helper: {
    fontFamily: 'Italic',
    fontSize: 8 * StylesVariables.responsiveHeightMulti,
    marginBottom:
      StylesVariables.spacing * StylesVariables.responsiveHeightMulti,
  },
});

const stylesWeb = {
  input: {
    height: 44 * StylesVariables.responsiveHeightMulti,
    borderRadius: 5,
    fontSize: StylesVariables.textFontSize + 1,
    backgroundColor: 'transparent',
    width: '100%',
    paddingLeft: 20,
    color: StylesVariables.AsteroideDark,
    fontFamily: 'Regular',
    fontSize: 16 * StylesVariables.responsiveHeightMulti,
  },
};
MapComponent.propTypes = {
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  changeAddress: PropTypes.func,
  radius: PropTypes.number,
  latitudeDelta: PropTypes.number,
  longitudeDelta: PropTypes.number,
  zoom: PropTypes.number,
  address: PropTypes.string,
  changeAddressText: PropTypes.func,
  completeAddress: PropTypes.func,
  editable: PropTypes.bool,
  errorMessage: PropTypes.string,
};

Mapp.propTypes = {
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  changeAddress: PropTypes.func,
  zoom: PropTypes.number,
  radius: PropTypes.number,
  address: PropTypes.string,
  changeAddressText: PropTypes.func,
  completeAddress: PropTypes.func,
  editable: PropTypes.bool,
  errorMessage: PropTypes.string,
};
export default MapComponent;
