import {Background, Button} from '../../components/UI';
import {Animated, Platform, View} from 'react-native';
import {Text} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useEffect, useRef} from 'react';

// Styles
import {authStyles} from './../auth/auth.styles';
import StylesVariables from './../../config/Styles/app.style';

// redux
import {AUTH_USER, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

// Clases
import {shareAction} from '../../config/utils/shareClass';
import ShopClass from '../../services/Shop.class';
const SuccessUpdateProduct = ({navigation, route}) => {
  useEffect(() => {
    fadeIn();
  }, []);
  const dispach = useDispatch();
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const fadeIn = () => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 1000,
    }).start();
  };

  const share = async () => {
    const {info} = route.params;
    shareAction(
        'Te invito a conocer mi '+info.name,
        `product/${info.id}`,
    );
  };
  return (
    <View
      style={[
        authStyles.interiorContainer,
        {backgroundColor: 'transparent'},
      ]}
    >
      <Background/>
      <View
        style={authStyles.responsiveContainer}
      >
        <Animated.View
          style={{opacity: fadeAnim}}
        >
          <Text
            style={[
              authStyles.cardTitle,
              {color: StylesVariables.SanLuis},
            ]}
          >
            Has actualizado tu producto con éxito
          </Text>
          <Text
            style={[
              authStyles.textStyle,
              {color: StylesVariables.Bajio80p},
            ]}
          >
            {
              'Empieza a compartirlo con tus clientes'
            }
          </Text>
          <Button
            uppercase={false}
            onPress={() => {
              share();
            }}
            id={'share_my_new_product'}
            title='Compartir producto'
            fullWidth
            mode='buttonsGColors'
          />
          <View
            style={{padding: 10}}
          />
          <Button
            uppercase={false}
            onPress={() => {
              navigation.navigate('HomeStore');
            }}
            title='Continuar'
            fullWidth
            mode={'buttonsGOutlineColors'}
          />
        </Animated.View>
      </View>
    </View>
  );
};

SuccessUpdateProduct.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};

export default SuccessUpdateProduct;
