import {Background, Button, TextInput, Navbar} from '../../components/UI';
import {buyAction} from '../../config/utils/shareClass';
import PropTypes from 'prop-types';
import React, {useState} from 'react';
import {FlatList, TouchableOpacity, View} from 'react-native';
import {Text} from 'react-native-paper';

// Styles
import {carStyles as styles} from './car.styles';
import StylesVariables from '../../config/Styles/app.style';

// icons
import {AntDesign} from '@expo/vector-icons';

const CarInfo = ({navigation, route}) => {
  const [items, setItems] = useState(route.params.car);
  const [form, setForm] = useState({name: '', number: ''});
  const [errorNumber, setErrorNumber] = useState(' ');
  const [errorMessage, setErrorMessage] = useState(' ');
  const changeForm = (key, value) => {
    setForm({
      ...form,
      [key]: value,
    });
  };
  const sendShopCart = () => {
    const {store} = route.params;
    const endPrice = finalPrice();
    let message =
    `¡Hola! Comparto los datos de mi pedido: %0aNombre: ${form.name}%0a`;

    for (const product of items) {
      message =
      message+
      `%0a${product.unit}x ${product.productName} ${'$'+product.price * product.unit}`;
    };
    message =
      message+`%0a--------%0a *Total*   *${endPrice}* %0a`;

    buyAction(
        message,
        `mitienda/${store.idShop}`,
        '52'+store.phoneNumber,
    );
  };

  const addCartAction = (product, action, index) => {
    const newArr = items;
    if (action == 'dimis') {
      if (product.unit == 1) {
        setItems(items.filter((item) => item.id !== product.id));
        return;
      }
      newArr[index] = {
        ...product,
        unit: product.unit -1,
      };
      setItems([
        ...newArr,
      ]);
      return;
    }
    newArr[index] = {
      ...product,
      unit: product.unit + 1,
    };
    setItems([...newArr]);
  };

  const validateNumber = (text) => {
    let newText = '';
    const numbers = '0123456789';
    for (let i = 0; i < text.length; i++) {
      if (numbers.indexOf(text[i]) > -1 ) {
        newText = newText + text[i];
      } else {
        setErrorNumber('Este campo solo acepta numeros');
      }
    }
    if (newText !== form.number) {
      setErrorNumber(' ');
    };
    changeForm('number', newText);
  };

  const validate = () => {
    if (form.number == '' || form.name == '') {
      setErrorMessage('Debes ingresar esta información');
      setErrorNumber('Debes ingresar esta información');
      return;
    };
    if (form.number.length < 10) {
      setErrorNumber('Porfavor Introduzca un numero valido');
      return;
    };
    sendShopCart();
  };

  const ItemRender = ({item, index}) => {
    return (
      <View
        style={styles.contentItemRow}
      >
        <View
          style={styles.rightItemRow}
        >
          <Text
            style={styles.textItemRow}
          >
            {item.productName}
          </Text>
        </View>
        <View
          style={styles.leftItemRow}
        >
          <TouchableOpacity
            style={{
              marginHorizontal: 8,
            }}
            onPress={() => addCartAction(item, 'dimis', index)}
          >
            <View
              style={styles.buttonsRadius}
            >
              <AntDesign
                name="minus"
                size={22 * StylesVariables.responsiveHeightMulti}
                color={StylesVariables.KometaDark}
              />
            </View>
          </TouchableOpacity>
          <Text
            style={styles.radiusText}
          >
            {item.unit}
          </Text>
          <TouchableOpacity
            style={{
              marginHorizontal: 8,
            }}
            onPress={() => addCartAction(item, 'add', index)}
          >
            <View
              style={styles.buttonsRadius}
            >
              <AntDesign
                name="plus"
                size={22 * StylesVariables.responsiveHeightMulti}
                color={StylesVariables.KometaDark}
              />
            </View>
          </TouchableOpacity>
        </View>
      </View>
    );
  };

  const finalPrice = () => {
    let price = 0;
    for (const product of items) {
      price = price + product.price * product.unit;
    }
    return price;
  };

  ItemRender.propTypes = {
    item: PropTypes.any,
  };

  return (
    <View
      style={styles.container}
    >
      <Background/>
      <Navbar
        navigation={navigation}
      />
      <View
        style={styles.responsiveContainer}
      >
        <View
          style={styles.topContainer}
        >
          <Text
            style={styles.title}
          >
            Información de pedido
          </Text>
          <View
            style={styles.row}
          >
            <Text
              style={styles.subTitle}
            >
              Productos
            </Text>
          </View>
          <FlatList
            style={{
              width: '100%',
              borderBottomWidth: 1,
              borderBottomColor: 'gray',
            }}
            data={items}
            renderItem={({item, index}) => {
              return (
                <ItemRender
                  item={item}
                  index={index}
                />
              );
            }}
          >
          </FlatList>
          <View
            style={styles.totalContainer}
          >
            <View
              style={styles.totalRight}
            >
              <Text
                style={styles.totalFont}
              >
                Total
              </Text>
            </View>
            <View
              style={styles.totalLeft}
            >
              <Text
                style={styles.totalFont}
              >
                ${finalPrice()}
              </Text>
            </View>
          </View>
        </View>
        <View
          style={styles.formContainer}
        >
          <View
            style={styles.row}
          >
            <Text
              style={styles.subTitle}
            >
              Rellena los campos solicitados
            </Text>
          </View>
          <TextInput
            value={form.name}
            label='Nombre Completo'
            placeholder='Ej: Mariana Torres'
            type={errorMessage == ' ' ? 'primary' : 'danger'}
            helper={errorMessage}
            onChangeText={(value) => {
              if (
                !/^[a-zA-Z\s]*$/.test(value)
              ) {
                setErrorMessage(
                    'Este campo no acepta numeros ni caracteres especiales',
                );
                return;
              };
              setErrorMessage(' ');
              changeForm('name', value);
            }}
            maxLength={30}
          />
          <View
            style={{padding: 5}}
          />
          <TextInput
            value={form.number}
            label='Número de WhatsApp'
            type={errorNumber == ' ' ? 'primary' : 'danger'}
            helper={errorNumber}
            placeholder='Ej: 2284934211'
            onChangeText={(value) => validateNumber(value)}
            maxLength={10}
          />
          <View
            style={{padding: 5}}
          />
          <Button
            title={'Hacer pedido por WhatsApp'}
            fullWidth={true}
            mode={'buttonsGColors'}
            onPress={validate}
          />
        </View>
      </View>
    </View>
  );
};

CarInfo.propTypes = {
  navigation: PropTypes.any,
  route: PropTypes.any,
};

export default CarInfo;
