import PropTypes from 'prop-types';
import React from 'react';
import {StyleSheet, TouchableOpacity, View} from 'react-native';

// Styles
import StylesVariables from '../../config/Styles/app.style';

const ButtonIconRounded = ({
  colorButton = '#65679E',
  Icon,
  onPress,
}) => {
  if (!Icon) {
    return null;
  }

  return (
    <TouchableOpacity
      onPress={onPress}
    >
      <View
        style={[
          styles.container,
          {backgroundColor: colorButton},
        ]}
      >
        <Icon/>
      </View>
    </TouchableOpacity>
  );
};

ButtonIconRounded.propTypes = {
  colorButton: PropTypes.string,
  Icon: PropTypes.func,
  onPress: PropTypes.func,
};
const styles = StyleSheet.create({
  container: {
    width: 25 * StylesVariables.responsiveHeightMulti,
    height: 25 * StylesVariables.responsiveHeightMulti,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 30 * StylesVariables.responsiveHeightMulti,
  },
});

export {ButtonIconRounded};
