import AppContainer from "./src/navigation";
import Constants from "expo-constants";
import * as Font from "expo-font";
import Places from "google-places-web";
import React, { useState, useEffect } from "react";
import styles from "./src/config/Styles/safe_area.style";
import { SafeAreaView, SafeAreaProvider } from "react-native-safe-area-context";
import TagManager from "react-gtm-module";
import { Provider } from "react-redux";
import configureStore from "./src/redux/store";
import ReactPixel from "react-facebook-pixel";
import { Provider as PaperProvider, DefaultTheme } from "react-native-paper";
import { Platform } from "react-native";
const apiKey = Constants.manifest.extra.geoApi;

const App = () => {
  Places.apiKey = apiKey;
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    loadFonts();
  }, []);
  const store = configureStore();
  const loadFonts = async () => {
    try {
      await Font.loadAsync({
        Regular: require("./assets/fonts/OpenSans-Regular.ttf"),
        Medium: require("./assets/fonts/OpenSans-Medium.ttf"),
        Bold: require("./assets/fonts/OpenSans-Bold.ttf"),
        SemiBold: require("./assets/fonts/OpenSans-SemiBold.ttf"),
        Italic: require("./assets/fonts/OpenSans-Italic.ttf"),
        Light: require("./assets/fonts/OpenSans-Light.ttf"),
        LightItalic: require("./assets/fonts/OpenSans-LightItalic.ttf"),
        NunitoBold: require("./assets/fonts/Nunito/Nunito-Bold.ttf"),
        Nunito: require("./assets/fonts/Nunito/Nunito-Regular.ttf"),
      });
      setLoading(true);
    } catch (e) {
      setLoading(false);
    }
  };
  const themeDark = DefaultTheme;
  const themeLigth = {
    ...DefaultTheme,
    background: "#FFFFFF",
  };
  if (!loading) {
    return null;
  }

  if (Platform.OS == "web") {
    const analytics = document.createElement("script");
    analytics.async = true;
    analytics.src = "https://www.googletagmanager.com/gtag/js?id=G-7KQ0SSWZ42";
    const analyticsContent = document.createElement('script');
    analyticsContent.innerHTML = `window.dataLayer = window.dataLayer || [];function gtag(){dataLayer.push(arguments);}gtag('js', new Date());gtag('config', 'G-7KQ0SSWZ42');`
    document.getElementsByTagName("head")[0].appendChild(analytics);
    document.getElementsByTagName("head")[0].appendChild(analyticsContent);
    const tagManagerArgs = {
      gtmId: "GTM-NVMBSV9",
    };
    TagManager.initialize(tagManagerArgs);
    const options = {
      autoConfig: true, // set pixel's autoConfig. More info: https://developers.facebook.com/docs/facebook-pixel/advanced/
      debug: false, // enable logs
    };
    const meta = document.createElement("meta");
    meta.name = "facebook-domain-verification";
    meta.content = "8s0y7egooja3ndr3f6s0h4wrrr6e9y";
    const googleMeta = document.createElement("meta");
    googleMeta.name = "google-site-verification";
    googleMeta.content = "W1H-7WGuULn2AOykfMwWWKRhMEMquDJpRhMEDyq8Png";
    document.getElementsByTagName("head")[0].appendChild(meta);
    document.getElementsByTagName("head")[0].appendChild(googleMeta);
  }

  return (
    <SafeAreaProvider>
      <Provider store={store}>
        <PaperProvider theme={themeLigth}>
          <AppContainer />
        </PaperProvider>
      </Provider>
    </SafeAreaProvider>
  );
};

export default App;
