import AsyncStorage from '@react-native-async-storage/async-storage';
import {Button} from './../../components/UI';
import {Text} from 'react-native-paper';
import PropTypes from 'prop-types';
import React, {useState, useEffect} from 'react';
import {LinearGradient} from 'expo-linear-gradient';
import {Image, Platform, View} from 'react-native';

// Styles
import {authStyles, gradient} from './auth.styles';


// Classes
import ShopClass from './../../services/Shop.class';

// redux
import {RENDER_MESSAGE, AUTH_USER, RENDER_LAYOUT} from '../../redux/constants';
import {useDispatch} from 'react-redux';

const InitScreen = ({navigation}) => {
  const [sessionNumber, setSessionNumber] = useState();
  const dispach = useDispatch();

  useEffect(() => {
    verifyIfOnSessionUser();
  }, []);

  const verifyIfOnSessionUser = async () => {
    let token;
    if (Platform.OS === 'web') {
      token = localStorage.getItem('kometa-token');
    } else {
      token = await AsyncStorage.getItem('kometa-token');
    };
    setSessionNumber(token);
  };

  const moveToStore = async () => {
    dispach({
      layout: {
        layout: true,
      },
      type: RENDER_LAYOUT,
    });
    const shop = new ShopClass();
    try {
      const userInfo = await shop.getShopByNumber(sessionNumber);
      dispach({
        userInfo: {
          number: userInfo.phoneNumber,
          userInfo,
        },
        type: AUTH_USER,
      });
    } catch (e) {
      dispach({
        message: {
          show: true,
          message: 'Sesion caducada, intentelo de nuevo.',
          status: 'N',
        },
        type: RENDER_MESSAGE,
      });
    }
    dispach({
      layout: {
        layout: false,
      },
      type: RENDER_LAYOUT,
    });
  };
  return (
    <View
      style={[
        authStyles.container,
        {overflow: 'hidden'},
      ]}
    >
      <View
        style={authStyles.decorationContainer}
      >
        <LinearGradient
          colors={gradient}
          style={authStyles.decorationContainer}
          start={{x: 0, y: 0.5}}
          end={{x: 1, y: 0.5}}
        />
        <View
          style={authStyles.topDecorationContainer}
        >
          <View
            style={authStyles.topCircle}
          />
        </View>
        <View
          style={authStyles.bottomDecorationContainer}
        >
          <View
            style={authStyles.bottomCircle}
          />
        </View>
      </View>
      <View
        style={authStyles.interiorContainer}
      >
        <View
          style={{
            flex: 1,
            paddingTop: 20,
            maxWidth: 900,
          }}
        >
          <View
            style={authStyles.logoContainer}
          >
            <View
              style={{flex: 1}}
            >
              <Image
                source={require('../../../assets/KometaMain.png')}
                style={authStyles.image}
                resizeMode={'contain'}
              />
            </View>
            <View
              style={{flex: 1, alignItems: 'flex-end'}}
            >
              {
                sessionNumber &&
                  <Button
                    type={'white'}
                    title={'Ir a mi tienda'}
                    onPress={() => moveToStore()}
                  />
              }
            </View>
          </View>
          <Text
            style={authStyles.cardTitle}
          >
            Tu primer
            <Text
              style={[
                authStyles.cardTitle,
                {color: 'black'},
              ]}
            >
              {' tienda '}
            </Text>
            en línea
          </Text>
          <Text
            style={authStyles.textStyle}
          >
            {
              '¡Crea, comparte por whatsapp y vende más!'
            }
          </Text>
          <View
            style={authStyles.buttonContainer}
          >
            <Button
              id={'home_screen_init_button'}
              category={'button'}
              action={'click'}
              uppercase={false}
              onPress={() => navigation.navigate('WhatsappRegister')}
              title='Crea tu tienda ahora'
              fullWidth={true}
              mode={'buttonsGColors'}
              textSty={authStyles.textButton}
              buttonSty={authStyles.buttons}
            />
            <Text
              style={authStyles.texButtonAnotation}
            >
              Sin costo en menos de 3 min.
            </Text>
          </View>
        </View>
      </View>
    </View>
  );
};

InitScreen.propTypes = {
  navigation: PropTypes.any,
};

export default InitScreen;
